import {
	getIndicatorCardWithAvatarJsx, 
	getAvatarElementJsx, 
	getLabelValueElementJsx, 
	getHighlightedLabelValueElementJsx
} from '@devsnc/library-opws-mapping-shared';

export const getIncidentCardContentJsx = item => {
	const {
		assignedTo,
		assignmentGroup,
		cmdbCi,
		priority
	} = item;

	const items = {
		1: {jsxBuilder: getAvatarElementJsx, field: assignedTo},
		2: {jsxBuilder: getLabelValueElementJsx, field: assignmentGroup},
		3: {jsxBuilder: getLabelValueElementJsx, field: cmdbCi},
		4: {jsxBuilder: getHighlightedLabelValueElementJsx, field: priority},
	};

	const cardJsx = getIndicatorCardWithAvatarJsx(items);
	return (
		<div>
			{cardJsx}
		</div>
	);
};