import {snOpwsRelatedItemsListActionTypes} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
	CARD_SELECTED: snOpwsRelatedItemsListActionTypes.CARD_SELECTED
};

const onCardSelection = coeffects => {
	const {
		action: {
			payload: {
				item,
				isSelected
			}
		},
		updateProperties
	} = coeffects;
	
	const selectedCardId = isSelected && item ? item.id : '';
	updateProperties({selectedCardId});
};


const effectHandlers = {
	onCardSelection
};

export default {
	ACTION_TYPES,
	effectHandlers
};