import {
	getLabelValueJsx, 
	formatComplexFieldToStringLabelValue
} from '@devsnc/library-opws-mapping-shared';

export const getAffectedCiCardContentJsx = item => {
	const {
		sysClassName,
		supportGroup
	} = item;

	const itemsCol1 = [
		formatComplexFieldToStringLabelValue(sysClassName)
	];

	const itemsCol2 = [
		formatComplexFieldToStringLabelValue(supportGroup)
	];

	return (
		<div className='columns-layout now-m-block--sm'>
			{getLabelValueJsx(itemsCol1)}
			{getLabelValueJsx(itemsCol2, true)}
		</div>
	);
};