import {INDICATOR_IDS} from '../map-shared/indicatorTypes';
import {
	getLabelValueJsx, 
	formatComplexFieldToStringLabelValue
} from '@devsnc/library-opws-mapping-shared';

export const getOutageCardContentJsx = (item, selectedTab) => {
	const {
		duration,
		begin,
		end
	} = item;

	let beginTime = '';
	let endTime = '';

	switch (selectedTab) {
		case INDICATOR_IDS.MAP_OUTAGES_CURRENT:
			beginTime = begin;
			break;
		case INDICATOR_IDS.MAP_OUTAGES_PLANNED:
		case INDICATOR_IDS.MAP_OUTAGES_PAST:
			beginTime = begin;
			endTime = end;
			break;
	}

	const itemsCol1 = [
		formatComplexFieldToStringLabelValue(beginTime),
		formatComplexFieldToStringLabelValue(duration)
	];

	const itemsCol2 = endTime !== '' ? [formatComplexFieldToStringLabelValue(endTime)] : [];

	return (
		<div className='columns-layout now-m-block--sm'>
			{getLabelValueJsx(itemsCol1)}
			{getLabelValueJsx(itemsCol2, true)}
		</div>
	);
};