import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {TYPE_IDS} from '../map-shared/itemTypes';
import effectGetAlertsForCis from './effectGetAlertsForCis';
import effectGetAlertsForBS from './effectGetAlertsForBS';
import {get as _get} from 'lodash';

const LIST_OF_BS = 'BS';
const LIST_OF_CI = 'CI';

const getQueryParams = args => {
	const {
		bsid,
		servicelist,
		timestamp,
		sortOption,
		firstRecord,
		infraPathCis,
		pathIds,
	} = args;

	return {
		serviceId: bsid,
		pathIds,
		ciIds: servicelist,
		timestamp: decodeURIComponent(timestamp),
		sortByField: sortOption.field,
		isOrderASC: (sortOption.order === 'asc'),
		firstRow: firstRecord,
		infraPathCis: infraPathCis ||''
	};
};

export const effectMapGetAlertItems = coeffects => {
	const {
		dispatch,
		updateProperties,
		properties: {
			relatedItemIds,
			relatedItemsResponse
		},
		action: {
			payload: {
				selectedType,
				selectedSortOption,
				firstRecord,
				forceRefresh = false
			}
		}
	} = coeffects;

	if (selectedType && selectedType.id !== TYPE_IDS.ALERTS) {
		LOGGING.warn(`effectMapGetAlertItems: effect called and did nothing, 
		because expected typeId ${TYPE_IDS.ALERTS}, but got ${selectedType.id}`);

		return;
	}

	const urlData = _get(relatedItemIds, 'ALERTS.urlData');

	if (!urlData)
		return;

	const {
		bsid,
		pathIds,
		servicelist,
		listof
	} = urlData.extArgs;

	const timestamp = forceRefresh ? 
		_get(urlData, 'params.timestamp') : 
		_get(relatedItemsResponse,'requestedTime');

	const sortOption = selectedType.sortOptions.find(sortOption => selectedSortOption === sortOption.id);

	const data = getQueryParams({
		bsid, 
		pathIds, 
		servicelist,
		timestamp,
		firstRecord,
		sortOption
	});
	updateProperties({cardsAreLoading:true});

	if (listof === LIST_OF_BS) 
		dispatch(effectGetAlertsForBS.ACTION_TYPES.MAP_GET_ALERTS_FOR_BS_REQUEST, {data});

	else if (listof === LIST_OF_CI) 
		dispatch(effectGetAlertsForCis.ACTION_TYPES.MAP_GET_ALERTS_FOR_CIS_REQUEST, {data});
};
