import {createHttpEffect} from '@servicenow/ui-effect-http';

const ACTION_TYPES = {
	MAP_GET_ALERTS_FOR_BS: 'MAP_GET_ALERTS_FOR_BS',
	MAP_GET_ALERTS_FOR_BS_REQUEST: 'MAP_GET_ALERTS_FOR_BS_REQUEST',
	MAP_GET_ALERTS_FOR_BS_REQUEST_STARTED: 'MAP_GET_ALERTS_FOR_BS_REQUEST_STARTED',
	MAP_GET_ALERTS_FOR_BS_REQUEST_SUCCEEDED: 'MAP_GET_ALERTS_FOR_BS_REQUEST_SUCCEEDED',
	MAP_GET_ALERTS_FOR_BS_REQUEST_FAILED: 'MAP_GET_ALERTS_FOR_BS_REQUEST_FAILED'
};


const getMapAlertsForBSeffectHandler = createHttpEffect('/api/sn_nocpit/nocpit/getAllAlertsForBsInMap', {
	method: 'POST',
	startActionType: ACTION_TYPES.MAP_GET_ALERTS_FOR_BS_REQUEST_STARTED,
	successActionType: ACTION_TYPES.MAP_GET_ALERTS_FOR_BS_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.MAP_GET_ALERTS_FOR_BS_REQUEST_FAILED
});


const effectHandlers = {
	getMapAlertsForBSeffectHandler,
};

export default {
	ACTION_TYPES,
	effectHandlers
};