import MAP_CONSTANTS from '../map-shared/constants';
import {getHeaderFieldsJsx as getHeaderFieldsJsxProxy} from './headerFieldsJsx';
import {getTimelineJsx as getTimelineJsxProxy} from './timelineJsx';

export const getTimelineJsx = ({mapHistoryState, timelineData}) =>
	mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT
		? null
		: getTimelineJsxProxy({timelineData});


export const getHeaderFieldsJsx = ({mapInitialData, isEm, serviceSeverity, mapHistoryState}) =>
	mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT
		? getHeaderFieldsJsxProxy({mapInitialData, isEm, serviceSeverity})
		: null;