import {t} from 'sn-translate';

const LABEL_CHANGE_TIME = t('Change time');

export const getChangeCardContentJsx = item => {
	const {time} = item;

	const labelValueItems = [
		{
			label: LABEL_CHANGE_TIME,
			value: time
		}
	];
	
	return (
		<div className='now-m-block-start--sm'>
			<now-label-value-stacked
				truncated
				size='sm'
				items={labelValueItems}/>
		</div>
	);
};