/**
 * These effects manage REST API request to bring topology changes when map is in current view.
 * Last changes on the service dispalyed, limited to 1 page.
 * The period of changes defaults to 1 week and can be configured with sys_property "noc_map.topo_changes_range" in days.
 */
import {
	get as _get, 
	mapKeys as _mapKeys, 
	identity as _identity, 
	sortBy as _sortBy
} from 'lodash';

import {createHttpEffect} from '@servicenow/ui-effect-http';
import {TYPE_IDS, ITEM_TYPES} from '../map-shared/itemTypes';

import {
	CONSTANTS, 
	LOGGING, 
	checkForError, 
	displayTimestamp
} from '@devsnc/library-opws-mapping-shared';
import MAP_CONSTANTS from '../map-shared/constants';

const {RELATED_ITEMS_PAGE_SIZE} = CONSTANTS.DEFAULTS;
const SORT_OPTIONS = ITEM_TYPES.find(type => type.id === TYPE_IDS.MAP_TOPO_CHANGES).sortOptions;

const ACTION_TYPES = {
	GET_HISTORY_CHANGES_REQUEST: 'GET_HISTORY_CHANGES_REQUEST',
	GET_HISTORY_CHANGES_REQUEST_STARTED: 'GET_HISTORY_CHANGES_REQUEST_STARTED',
	GET_HISTORY_CHANGES_REQUEST_SUCCEEDED: 'GET_HISTORY_CHANGES_REQUEST_SUCCEEDED',
	GET_HISTORY_CHANGES_REQUEST_FAILED: 'GET_HISTORY_CHANGES_REQUEST_FAILED',
};

const CONVERT_DAY_TO_MS = 1000 * 60 * 60 * 24; 
export const DEFAULT_RANGE_DAYS = 7;

export const getMapChangeItems = coeffects => {
	const {
		dispatch,
		updateProperties,
		updateState,
		properties: {
			serviceId,
			userMeta: {topologyChangesRange},
			relatedItemIds
		},
		state: {
			topoChangesItemsCache
		},
		action: {
			payload: {
				selectedType,
				forceRefresh
			}
		}
	} = coeffects;
	
	if (selectedType && selectedType.id !== TYPE_IDS.MAP_TOPO_CHANGES) {
		LOGGING.warn(`getMapChangeItems: effect called and did nothing, 
		because expected typeId ${TYPE_IDS.MAP_TOPO_CHANGES}, but got ${selectedType && selectedType.id}`);

		return;
	}

	const updatedTimestamp = _get(relatedItemIds, `[${TYPE_IDS.MAP_TOPO_CHANGES}].urlData.timestamp`);
	const currentTimestamp = topoChangesItemsCache.timestamp || null;
	
	if (!forceRefresh && currentTimestamp && currentTimestamp === updatedTimestamp && topoChangesItemsCache.items) {
		//retrieving cached items, skipping request.
		dispatch(ACTION_TYPES.GET_HISTORY_CHANGES_REQUEST_SUCCEEDED, topoChangesItemsCache.items);
		return;
	} 

	const range = CONVERT_DAY_TO_MS * (Number(topologyChangesRange) || DEFAULT_RANGE_DAYS);
	const endTime = Date.now();
	const startTime = endTime - range;

	updateState({
		operation: 'set',
		path: 'topoChangesItemsCache.timestamp',
		value: updatedTimestamp
	});

	updateProperties({cardsAreLoading: true});

	dispatch(() => ({
		type: ACTION_TYPES.GET_HISTORY_CHANGES_REQUEST,
		payload: {serviceId, startTime, endTime}
	}));
};


const getHistoryChanges = createHttpEffect('/api/sn_itom_map_app/embedded_map/changes', {
	method: 'GET',
	queryParams: ['serviceId', 'startTime', 'endTime'],
	startActionType: ACTION_TYPES.GET_HISTORY_CHANGES_REQUEST_STARTED,
	successActionType: ACTION_TYPES.GET_HISTORY_CHANGES_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.GET_HISTORY_CHANGES_REQUEST_FAILED
});

const filterByCiIds = (ciIdsLookup, change) => (change && ciIdsLookup[change.ciId]);

const generateItem = (userMeta, change = {}) => {
	const {
		tz,
		dateTimeFormat
	} = userMeta;

	const {
		id,
		message: description = '',
		time: timestamp = -1,
		ciId
	} = change;

	if (!change.id) {
		LOGGING.warn(`Expected change object to have id, got ${id}`);
		return null;
	}

	const time = displayTimestamp(timestamp, tz, dateTimeFormat);

	return {
		id,
		description,
		time,
		ciId
	};
};

const filterChangeCards = ({changes, ciIdsLookup}) => {
	const filtered = ciIdsLookup ? 
		changes.filter(filterByCiIds.bind(null, ciIdsLookup)) : 
		changes;

	return filtered;
};

/**
 * Formats change items returned from service into `items` array ready for the items-list.
 * @param {*} args:
 * `changes` - returned payload fro mteh server - array of change items DTOs.
 * `ciIdsLookup` - set (object) of CIs to filter by.
 * `sortConfig` - currently selected sorting option - the object with `field` and `order`.
 */
const formatChangeCards = ({filteredChanges, sortConfig, userMeta}) => {
	const sorted = sortConfig ? 
		_sortBy(filteredChanges, sortConfig.field) : 
		filteredChanges;

	if (sortConfig && sortConfig.order === 'desc')
		sorted.reverse();

	const items = sorted
		.slice(0, RELATED_ITEMS_PAGE_SIZE)
		.map(generateItem.bind(null, userMeta))
		.filter(Boolean);

	return items;
};

const historyChangesResponse = coeffects => {
	LOGGING.log('historyChangesResponse');
	const {
		dispatch,
		updateState,
		updateProperties,
		action: {
			payload: changes
		},
		state: {
			relatedItemsRefreshParams: {
				selectedSortOption
			}
		},
		properties: {
			relatedItemIds,
			userMeta,
			timelineData: {
				selectedTimelineEvents = null
			}
		}
	} = coeffects;

	const errors = checkForError({payload: changes, dispatch, caller: 'historyChangesResponse'});
	if (errors) {
		updateState({
			topoChangesItemsCache: {}
		});
		updateProperties({cardsAreLoading: false});
		return;
	}

	const ciIds = _get(relatedItemIds, `${TYPE_IDS.MAP_TOPO_CHANGES}.urlData.ciFilter`, null);
	const ciIdsLookup = ciIds ? _mapKeys(ciIds, _identity) : null;
	const sortConfig = SORT_OPTIONS.find(option => option.id === selectedSortOption);

	const filteredChanges = filterChangeCards({changes, ciIdsLookup});
	const items = formatChangeCards({filteredChanges, sortConfig, userMeta});
	updateState({
		operation: 'set',
		path: 'topoChangesItemsCache.items',
		value: changes
	});

	updateProperties({
		cardsAreLoading: false,
		filteredItems: filteredChanges,
		relatedItemsResponse: {
			items,
			lastUpdated: Date.now(), //this attribute is not in use when map is in history mode. Only for refresh functionality.
			totalRecords: filteredChanges.length,
			//detecting the filter state for the side panel. Assuming that map element selection and timeline event selection never overlap (cancel out).
			filterApplied: {
				type: ciIdsLookup ? MAP_CONSTANTS.CONTEXT_SELECTION_STATES.MAP : 
					selectedTimelineEvents !== null ? 
						MAP_CONSTANTS.CONTEXT_SELECTION_STATES.TIMELINE : 
						null
			}
		}
	});
};

const historyChangesFailure = coeffects => {
	LOGGING.log('historyChangesFailure');

	const {
		updateState,
		updateProperties,
		dispatch,
		action: {
			payload
		}
	} = coeffects;

	updateState({
		topoChangesItemsCache: {}
	});

	updateProperties({cardsAreLoading: false});
	
	dispatch(() => ({
		type: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED, 
		payload
	}));
};

const effectHandlers = {
	getMapChangeItems,
	getHistoryChanges,
	historyChangesResponse,
	historyChangesFailure
};

export default {
	ACTION_TYPES,
	effectHandlers
};