import {
	isFunction as _isFunction, 
	isArray as _isArray,
	isString as _isString,
	identity as _identity,
	get as _get
} from 'lodash';
import {t} from 'sn-translate';
import {Fragment, dangerouslyCreateElementFromString} from '@servicenow/ui-renderer-snabbdom';
import {
	getDisplayValue, 
	genUniqueSysId
} from '@devsnc/library-opws-mapping-shared'; //noc-element-with-tooltip, sn-noc-notifications 
import MAP_CONSTANTS from '../map-shared/constants';
import {settingsMenuSections} from './settingsMenuSections';
import {ACTION_TYPES as mapSettingsMenuActions} from '../map-settings-menu-behavior/mapSettingsMenuBehavior';
import '@servicenow/now-image';
import '@servicenow/now-toggle';
import '@servicenow/now-button';
import '@servicenow/now-modal';
import '@servicenow/now-heading';
import '@servicenow/now-dropdown';
import effectDropdownItemSelected from './effectDropdownItemSelected';

const ICON_SIZE_SM = 16;

const LABEL_HEADER = t('Header');
const LABEL_MORE = t('More');
const SETTINGS_MODAL_TITLE = t('Map display settings');
const LABEL_OPEN_SETTINGS = t('Open map display settings');
const LABEL_OPEN_FULL_MAP = t('Advanced map');
const LABEL_OPEN_HISTORY_MAP = t('History map');

const generateAdditionalInfoRow = row => 
	(<div>{row}</div>);

const generateAdditionalInfoJsx = item => {
	if (!_isFunction(item.isAdditionalInfoShown) || item.isAdditionalInfoShown()) {
		const additionalInfoRows = _isFunction(item.additionalInfo) ? item.additionalInfo() : item.additionalInfo;
		const rowsArr = _isArray(additionalInfoRows) ? additionalInfoRows : [additionalInfoRows];
		const additionalInfo = rowsArr.map(generateAdditionalInfoRow);

		return additionalInfo || null;
	}

	return null;
};

const parseLabel = label => {
	if (!_isString(label)) {
		return {
			labelJsx: '',
			labelPlainText: ''
		};
	}

	if (label.includes('<span')) {
		//parsing HTML label as vnode and plainText
		const labelVnode = dangerouslyCreateElementFromString(label); //assuming label is a single root HTML
		const labelPlainText = _get(labelVnode, 'elm.innerText', '');

		return {
			labelJsx: labelVnode,
			labelPlainText
		};
	}

	//plain text label
	return {
		labelJsx: label,
		labelPlainText: label
	};
};

const generateToggleLabelItem = (sectionId, item, idx) => {
	//if item not shown, when don't render it
	if (_isFunction(item.isShown) && !item.isShown())
		return null;

	const itemId = String(item.id) || `fallback-item-id-${idx}`;
	const additionalInfoId = `description_${itemId}`;
	const descriptionId = `description_hidden_${itemId}`;
	const additionalInfo = generateAdditionalInfoJsx(item);

	const descriptionFieldJsx = item.description ?
		<label className='now-a11y-label' id={descriptionId}>
			{item.description}
		</label> :
		null;

	const labelledByIds = [
		sectionId,
		additionalInfoId,
		item.description ? descriptionId : ''
	].join(' '); 

	const {labelJsx, labelPlainText} = parseLabel(item.label);

	const iconJsx = item.icon ?
		<now-image 
			className='field-icon now-m-inline-end--sm' 
			width={ICON_SIZE_SM}
			height={ICON_SIZE_SM}
			src={item.icon} 
			alt={labelPlainText}/> : 
		null;

	
	const valueJsx = (
		<div className='now-align flex-column now-m-block-start--sm'>
			<span className='now-align -block-start flex-grow'>
				<label 
					className='field-label now-align -inline-start flex-grow' 
					id={sectionId}>
					{labelJsx}
				</label>

				{descriptionFieldJsx}
				{iconJsx}

				<noc-element-with-tooltip
					className='now-align -inline-end'
					tooltip={item.description || null}>

					<now-toggle
						append-to-payload={{itemId}}
						manage-checked
						props-checked={Boolean(item.enabled)}
						props-disabled={_isFunction(item.isDisabled) && item.isDisabled()}
						aria-labelledby={labelledByIds} />

				</noc-element-with-tooltip>
			</span>
			
			<label 
				className='field-description now-align -block-end'
				id={additionalInfoId}>
				{additionalInfo}
			</label>
		</div>
	);
	
	return valueJsx;
};

const firstLetterCapital = str => 
	_isString(str) ? `${str.charAt(0).toLocaleUpperCase()}${str.substr(1).toLocaleLowerCase()}` : '';

const generateSectionContainerJsx = (model, section, index) => {
	if (!model)
		return null;

	if (section.keys.length === 0) 
		return null;

	const sectionId = `section_${section.id}`;
	const sectionItems = section.keys.map(key => model[key]).filter(Boolean);
	const togglesJsx = sectionItems.map(generateToggleLabelItem.bind(null, sectionId)).filter(Boolean);

	//canShow() conditions per each item can filter out everything. If list is empty, secion should not be rendered.
	if (togglesJsx.length === 0)
		return null;

	const cssClassSection = {
		section: true,
		'now-m-inline--xl': true,
		'now-m-block-start--xl': index !== 0
	};
	
	return (
		<section class={cssClassSection}>
			<now-heading
				className='section-legend now-m-block-end--sm' 
				id={sectionId}
				label={firstLetterCapital(String(section.label))}
				level='2'
				variant='title-primary'
				has-no-margin/>

			<div className='section-internals now-p-block-start--md'>
				{togglesJsx}
			</div>
		</section>
	);
};

const openSettings = dispatch => () => 
	dispatch(mapSettingsMenuActions.MAP_TOGGLE_SETTINGS_VIEW, {isOpen: true});


const openHistoryMap = ({serviceId, isEm, dispatch}) => () => {
	//for history map, generating a different unique id to open separate tab side by side with current map.
	//invoking genUniqueSysId will rotate each character twice and create another unique id.
	const fakeId = genUniqueSysId(genUniqueSysId(serviceId));
	dispatch('ITEM_SELECTED', {
		table: 'app_service_history_map',
		sys_id: fakeId,
		params: {
			isEm,
			serviceId,
			isHistory: true
		},
		//history/compare only exists in discovered service.
		parent_table: 'cmdb_ci_service_discovered',
		parent_sys_id: serviceId
	});
};

const getMapHeaderJsx = mapInitialData => 
	mapInitialData ? getDisplayValue(mapInitialData.name) : '';


const moreItems = [
	{
		id: effectDropdownItemSelected.ITEM_IDS.FULL_MAP,
		label: LABEL_OPEN_FULL_MAP,
		icon: 'open-link-outline'
	}
];

const getHeaderButtonsJsx = ({isHistory, serviceId, isEm, isBlocked, dispatch}) => {
	const currentMapButtons = isHistory ? 
		null : (
			<Fragment>
				<now-button
					label={LABEL_OPEN_HISTORY_MAP}
					size='md'
					disabled={isBlocked}
					on-click={isBlocked ? _identity : openHistoryMap({serviceId, isEm, dispatch})}
				/>

				<now-dropdown
					className='now-m-inline-start--sm'
					manage-selected-items
					hide-caret 
					hide-label 
					icon='ellipsis-h-outline'
					select='none' 
					selected-items='[]' 
					size='md' 
					variant='secondary'
					tooltip-content={LABEL_MORE}
					items={moreItems}
					append-to-payload={{dropdownId: effectDropdownItemSelected.DROPDOWN_ID}}
				/>
			</Fragment>
		);
	

	return (
		<Fragment>
			<now-button-iconic
				className='now-m-block-end--md'
				icon='gear-outline'
				size='md'
				on-click={openSettings(dispatch)}
				tooltip-content={LABEL_OPEN_SETTINGS}
				config-aria={{'aria-label': LABEL_OPEN_SETTINGS}}
			/>
				
			{currentMapButtons}
		</Fragment>
	);

};

export default (state, {dispatch}) => {
	const {
		serviceId, 
		mapId,
		mapInitialData = {}, 
		mapSettings, 
		isEm,
		notifications = [],
		isHistory,
		isLoading,
		isBlocked
	} = state.properties;

	if (!mapId)
		return;

	const {model, isOpen} = (mapSettings || {});
	const formJsx = settingsMenuSections.map(generateSectionContainerJsx.bind(null, model));
	const mapHeader = getMapHeaderJsx(mapInitialData);
	const headerButtonsJsx = isLoading ? null : getHeaderButtonsJsx({isHistory, serviceId, isEm, isBlocked, dispatch});
	const notificationsComponentId = `${MAP_CONSTANTS.MAP_APP_NOTIFICATIONS_COMPONENT_ID_BASE}_${mapId}`;

	return (
		<header 
			className='now-align -block-start top-navbar' 
			id={`${mapId}_landmark_header`}
			aria-label={LABEL_HEADER}
		>

			<span className='now-align service-header'>
				<div className='now-align -inline-start'>
					<now-heading
						className='noc-service-map-header'
						label={mapHeader}
						level='1'
						variant='header-primary'
						has-no-margin/>

					<div className='now-align -inline-end navbar-buttons'>
						{headerButtonsJsx}

						<now-modal
							manage-opened
							opened={isOpen}
							append-to-payload={{mapId}}
							content-full-width
							size='md'
							header-label={SETTINGS_MODAL_TITLE}>
							
							<div className='map-settings-form now-m-block-end--md'>
								{formJsx}
							</div>

						</now-modal>

					</div>
				</div>
				
			</span>

			<sn-noc-notifications
				component-id={notificationsComponentId}
				className='notifications'
				notifications={notifications}/>
		</header>
	);
};