/**
 * effectResolveHistoryState:
 * When displayRange for the tiemline is resolved and all required data is set,
 * this effect updated input props fot the change cards and for the timeline.
 */
import {t} from 'sn-translate';
import {map as _map, isFunction as _isFunction} from 'lodash';
import {CONSTANTS, LOGGING, snOpwsRelatedItemsListActionTypes} from '@devsnc/library-opws-mapping-shared';
import timelineConstants from './timelineConstants';
import {resolveMapHistoryState, sanitizeHistoryRange, formatRange, getEmptyTimelineData} from './timelineFunctors';
import {dispatchEventToMap} from '../map-container/eventBus';
import MAP_CONSTANTS from '../map-shared/constants';
import {ACTION_TYPES as MAP_SETTINGS_SET_ACTION_TYPES} from '../map-settings-menu-behavior/mapSettingsMenuBehavior';
import effectReceiveHistoryData from './effectReceiveHistoryData';

const {
	TIMELINE_TYPE_TO_BUBBLE_GROUP,
	TIMELINE_ITEM_TYPES,
	BUBBLE_STYLE,
	CHANGE_LABELS,
	SEVERITY_LABELS
} = timelineConstants;

const APPROVED_CHANGE_DESCRIPTION = t('Approved change: {0}', 'MESSAGE');
const NOTIFICATION_SELECT_SHORTER_RANGE = t('The time range contained too many changes. The window shows the service history for a narrower range.');
const NOTIFICATION_SHORTER_RANGE_SELECTED = t('The selected time range contained too many changes. Narrow the time range and then focus on the relevant changes.');
const NOTIFICATION_CANNOT_DISPLAY_LIMIT = t('The time range contained too many changes. The map cannot be loaded. Try again later or call customer support.');

const ACTION_TYPES = {};

const groupTimelineRanges = (ranges, range) => {
	const {
		data: {newSeverity}
	} = range;

	const group = ranges[newSeverity] || [];
	group.push(range);

	return {
		...ranges,
		[newSeverity]: group
	};
};

const resolveChangeType = ({type}) => TIMELINE_TYPE_TO_BUBBLE_GROUP[type];

/**
 * For approved changes, the description has a prefix "Approved change:".
 * @param change change object as arrived from the timeline API.
 * @return description for the given change object
 */
const resolveApprovedChangeDescription = change => {
	const {type, approved, message} = change || {};

	const groupName = TIMELINE_TYPE_TO_BUBBLE_GROUP[type];

	if (groupName === TIMELINE_ITEM_TYPES.TOPOLOGY && approved)
		return APPROVED_CHANGE_DESCRIPTION.replace('MESSAGE', message);

	return message;
};

const groupTimelineEvents = ({events, _eventsIndex}, change) => {
	const groupName = resolveChangeType(change);

	//skipping unregistered change/bubble types.
	if (!groupName) 
		return {events, _eventsIndex};

	const group = events[groupName] || [];

	const {time: timestamp} = change;
	const description = resolveApprovedChangeDescription(change);

	const event = {
		data: change,
		timestamp,
		description
	};

	group.push(event);
	_eventsIndex.set(change.id, event);

	return {
		events: {
			...events,
			[groupName]: group
		},
		_eventsIndex
	};
};

const filterChangesByDisplayRange = (candidateRange, change) =>
	change.time && change.time >= candidateRange.start && change.time <= candidateRange.end;

const filterSeveritiesByDisplayRange = (candidateRange, sev) =>
	sev.start && sev.end && sev.end >= candidateRange.start && sev.start <= candidateRange.end;

const resolveSeverityColor = (severitiesGetter, severity = '') =>
	(_isFunction(severitiesGetter) ? severitiesGetter(severity) : null) || null;

const mapRangeCategories = ({severitiesGetter}, bulk, severity) =>
	Number(severity) === 0 ? 
		null :
		{
			name: severity,
			color: resolveSeverityColor(severitiesGetter, severity),
			label: SEVERITY_LABELS[severity] || '',
			ranges: bulk
		};

const mapEventCategories = (bulk, type) => ({
	name: type,
	...(BUBBLE_STYLE[type] || {}),
	...(CHANGE_LABELS[type] || {}),
	events: bulk
});

const mapChangeToId = ch => ch.id;

/**
 * Finds fixed start timestamp for candidate range that will ensure the range within the changes limit.
 * If there's no such range, returns null.
 * @param {*} param0 
 * @return {Number|null} `start` timestamp for new candidate range, or null if cannot display the timeline.
 * If `null` returned, this function dispatches notification for user with the failure reason.
 */
const fixCandidateRangeStart = ({displayChanges, changesLimit, dispatch, upperDateLimit}) => {
	//we want to display `changesLimit` changes (and it's known there are more changes in array)
	const lowerIdx = displayChanges.length - changesLimit;
	const lowerBoundary = displayChanges[lowerIdx];
	const candidateStart = lowerBoundary.time;
	const changeBefore = displayChanges[lowerIdx - 1]; //always exists because (displayChanges.length > changesLimit)

	if (candidateStart !== changeBefore.time)
		return candidateStart;

	//cannot use candidateStart, because previous change has exactly the same timestamp (aggregated changes).

	//find next change that has another timestamp
	const reducedRange = displayChanges.slice(lowerIdx);
	const updLowerboundary = reducedRange.find(ch => ch.time > candidateStart);

	//cannot display the timeline: nothing to display for set changes limit, because most recent changes are aggregated.
	if (!updLowerboundary) {

		//attempt to select an empty range between the last change and NOW.
		const lastChangeTime = displayChanges[displayChanges.length - 1].time + 1;

		if (upperDateLimit - lastChangeTime > timelineConstants.UNITS.MINUTE) 
			return lastChangeTime;

		dispatch(CONSTANTS.ACTION_TYPES.NOC_SHOW_NOTIFICATION, {
			status: 'high',
			icon: '',
			content: NOTIFICATION_CANNOT_DISPLAY_LIMIT,
			type: 'acknowledge'
		});

		return null;
	}

	return updLowerboundary.time;
};

/**
 * Detects and resolves cases when candidate range contains too many changes.
 * @return {Boolean} `true` if can use display range (can continue). 
 * `false` if max changes limit detected and resolved, means current candidate range cannot be used.
 */
const resolveChangesLimit = ({
	serviceCreationTime,
	upperDateLimit,
	displayChanges,
	changesLimit,
	dispatch,
	updateProperties,
	updateState,
	timelineData = {}
}) => {

	if (displayChanges.length <= changesLimit)
		return true;

	const {displayRange: currentDisplayRange} = timelineData;

	if (currentDisplayRange) {
		//candidate range exceedes the #changes limit, discarding

		//changing total range in attempt to disable current operation
		const totalRange = {
			start: serviceCreationTime,
			end: upperDateLimit
		};
		
		dispatch(CONSTANTS.ACTION_TYPES.NOC_SHOW_NOTIFICATION, {
			icon: '',
			content: NOTIFICATION_SELECT_SHORTER_RANGE,
			type: 'acknowledge'
		});

		updateProperties({
			timelineCandidates: {
				displayRange: null
			},
			timelineData: {
				...timelineData,
				totalRange
			}
		});

		updateState({
			loadingState: MAP_CONSTANTS.LOADING_STATES.DONE
		});
	} else {
		//first loading of the timeline: need to reduce candidate range until it's possible to display

		const candidateStart = fixCandidateRangeStart({displayChanges, changesLimit, dispatch, upperDateLimit});

		if (!candidateStart) {
			//stopping with error state

			updateState({
				loadingState: MAP_CONSTANTS.LOADING_STATES.ERROR
			});

			updateProperties({
				timelineCandidates: {
					displayRange: null
				}
			});

			return false;
		}

		dispatch(CONSTANTS.ACTION_TYPES.NOC_SHOW_NOTIFICATION, {
			icon: '',
			content: NOTIFICATION_SHORTER_RANGE_SELECTED,
			type: 'acknowledge'
		});

		const candidates = {
			displayRange: {
				start: candidateStart,
				end: Date.now()
			}
		};

		//fixing candidate range
		updateProperties({
			timelineCandidates: candidates
		});

		//re-launch this effect with updated candidate range
		dispatch(effectReceiveHistoryData.ACTION_TYPES.MAP_HISTORY_DATA_SET);
	}

	return false;
};

/**
 * When new visible range applies, the timeline data is recalculated here.
 * This effect assumes that `allChanges` and `severityRanges` have all the data for the candidate display range.
 * Selection states are reset on the timeline.
 */
const resolveHistoryState = coeffects => {
	const {
		state: {
			severitiesGetter,
			relatedItemsRefreshParams,
			sysProperties: {
				[MAP_CONSTANTS.SYS_PROP_MAX_HISTORY_CHANGES_DISPLAYED] : maxHistoryChangesDisplayed
			} = {}
		},
		properties: {
			mapId,
			allChanges,
			severityRanges,
			serviceCreationTime,
			upperDateLimit,
			timelineCandidates: {displayRange},
			timelineData
		},
		dispatch,
		updateProperties,
		updateState
	} = coeffects;

	//If no candidate displayRange, then in prefetch mode, do nothing
	if (!displayRange) {
		LOGGING.debug('timelineCandidates.displayRange is not defined. Assuming prefetch and skipping...', {caller: 'resolveHistoryState'});
		return;
	}
	
	const sanitizedRange = sanitizeHistoryRange({...displayRange, serviceCreationTime, upperDateLimit});
	const displayChanges = allChanges.filter(filterChangesByDisplayRange.bind(null, sanitizedRange));

	const limitPassed = resolveChangesLimit({
		serviceCreationTime,
		upperDateLimit,
		displayChanges, 
		changesLimit: maxHistoryChangesDisplayed, 
		timelineData,
		dispatch,
		updateProperties,
		updateState
	});

	if (!limitPassed)
		return;
	
	const eventsGrouping = displayChanges.reduce(groupTimelineEvents, {events: {}, _eventsIndex: new Map()});
	const {events: eventsMapping, _eventsIndex} = eventsGrouping;

	const events = _map(eventsMapping, mapEventCategories);

	const severityRangeFilter = filterSeveritiesByDisplayRange.bind(null, sanitizedRange);
	const filteredSeverities = severityRanges.filter(severityRangeFilter);

	const rangesMapping = filteredSeverities.reduce(groupTimelineRanges, {});
	const ranges = _map(rangesMapping, mapRangeCategories.bind(null, {severitiesGetter})).filter(Boolean);

	//decide history|compare state and notify angular
	const mapHistoryState = resolveMapHistoryState({displayChanges});
	//mapHistoryState can be either HISTORY or COMPARE here.
	const payloadForMap = {
		isCompare: mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.COMPARE,
		from: sanitizedRange.start,
		to: sanitizedRange.end,
		timestamp: sanitizedRange.end,
		checkpoints: displayChanges.map(mapChangeToId)
	};

	const totalRange = {
		start: serviceCreationTime,
		end: upperDateLimit
	};
	
	LOGGING.debug(`Setting props for the timeline.
		displayRange: ${formatRange(sanitizedRange.start, sanitizedRange.end)}
		totalRange: ${formatRange(totalRange.start, totalRange.end)}`,
	{caller: 'resolveHistoryState'}
	);

	const newTimelineData = {
		...getEmptyTimelineData(),
		events,
		ranges,
		displayRange: sanitizedRange,
		totalRange,
		_eventsIndex
	};

	updateProperties({
		displayChanges,
		mapHistoryState,
		timelineData: newTimelineData,
		timelineCandidates: {}
	});

	dispatchEventToMap(MAP_CONSTANTS.EVT_WS_SET_MAP_HISTORY_STATE, payloadForMap, mapId);

	//this action recomputes options for cards lists
	dispatch(() => ({type: MAP_SETTINGS_SET_ACTION_TYPES.MAP_SETTINGS_SET}));


	//this action updates changes cards
	dispatch(() => ({
		type: snOpwsRelatedItemsListActionTypes.REFRESH_REQUESTED,
		payload: {
			...relatedItemsRefreshParams,
			forceRefresh: true
		}
	}));

};

const effectHandlers = {
	resolveHistoryState
};

export default {
	ACTION_TYPES,
	effectHandlers
};
