import {snOpwsRelatedItemsListActionTypes} from '@devsnc/library-opws-mapping-shared';
import mapPropertiesActionTypes from '../map-side-panel/actions/sn-map-properties/actionTypes';
import MAP_CONSTANTS from '../map-shared/constants';
import {clearTimelineSelections, clearMapSelection} from '../map-shared/setHistoryChangesFilter';

const ACTION_TYPES = {
	RELATED_ITEMS_BACK: snOpwsRelatedItemsListActionTypes.BACK_ACTION,
	PROPERTIES_BACK: mapPropertiesActionTypes.BACK_ACTION
};

const propertiesBack = ({dispatch}) => 
	clearMapSelection(dispatch);

const relatedItemsBack = coeffects => {
	const {
		state: {
			relatedItemsRefreshParams
		},
		properties: {
			relatedItemsResponse: {
				filterApplied
			}
		},
		dispatch,
		updateProperties
	} = coeffects;

	const filterType = (filterApplied && filterApplied.type || null);

	if (filterType === MAP_CONSTANTS.CONTEXT_SELECTION_STATES.TIMELINE) {
		//clear timeline selection state
		clearTimelineSelections(updateProperties);
		
		//Trigger refresh of related items panel.
		dispatch(() => ({
			type: snOpwsRelatedItemsListActionTypes.REFRESH_REQUESTED, 
			payload: {
				...relatedItemsRefreshParams,
				forceRefresh: true
			}
		}));
	}

	//map selections (and markings) should be cleared regardless the filter state.
	clearMapSelection(dispatch);
};

const effectHandlers = {
	propertiesBack,
	relatedItemsBack
};

export default {
	ACTION_TYPES,
	effectHandlers
};