import {get as _get} from 'lodash';
import {stringify} from './json';
import constants from '../map-shared/constants';
import mapEventsWhitelist from './mapEventsWhitelist';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import effectInitHistoryMode from '../behavior-service-history/effectInitHistoryMode';

const DOM_EVENT_BUS_ID = 'DOM_EVENT_BUS_ID';
const EVT_MAP_TO_WORKSPACE = 'EVT_MAP_TO_WORKSPACE';
//first event dispatched from map to pass seismic-to-map callback.
const EVT_CONNECT_MAP_TO_SEISMIC_BUS = 'EVT_CONNECT_MAP_TO_SEISMIC_BUS';

/**
 * Mapping of eventBus objects per each map instance.
 * Mapped by mapId (mapId is unique for each map application and has nothing to do with serviceId):
 * `eventBusElm` - the element inside the map iframe, on which event listeners are set.
 * `connectEventListener` - one time listener that is used t oreceive first ping fro mmap with workspaceEventReceiver() function.
 * `mapEventListener` - original listener function for event arriving from map (EVT_MAP_TO_WORKSPACE).
 * `workspaceEventReceiver` - the fucntion received from hte angular map that should be called to send event back to the map.
 */
const contextMap = new Map();

const connectMapListener = (mapId, dispatch) => event => {
	if (!event)
		return;

	const context = contextMap.get(mapId);
	event.stopPropagation();
	event.stopImmediatePropagation();

	const {channelFn, stopMe} = event.detail;
	context.workspaceEventReceiver = channelFn;
	stopMe();

	//the moment we have event bus connected to the iframe, trigger history mode init (if applicable)
	dispatch(effectInitHistoryMode.ACTION_TYPES.MAP_INIT_HISTORY);
};

const createMapEventListener = dispatch => event => {
	if (!event)
		return;

	event.stopPropagation();
	event.stopImmediatePropagation();

	const {name, args} = event.detail;

	if (mapEventsWhitelist[name]) {
		LOGGING.debug(`NOC/MAP: Map event received ${name}, ${stringify(args)}`);
		dispatch(constants.NOC_EVENT_FROM_MAP_ARRIVED, {name, args});
	}
};

const getEventBusElement = iframeRef => {
	const iframeElm = iframeRef.current || iframeRef;
	const innerDoc = iframeElm.contentDocument || _get(iframeElm, 'contentWindow.document');
	const currEventBusElm = innerDoc ? innerDoc.getElementById(DOM_EVENT_BUS_ID) : null;

	return currEventBusElm || null;
};

export const initEventBus = (dispatch, iframeRef, mapId) => {
	LOGGING.debug(`NOC/MAP: initEventBus with mapId: ${mapId}`);
	const danglingContext = contextMap.get(mapId);
    
	if (danglingContext)
		destroyMapListeners(mapId);

	const eventBusElm = getEventBusElement(iframeRef);

	if (!eventBusElm)
		return false;


	const context = {
		eventBusElm,
		mapEventListener: createMapEventListener(dispatch),
		connectEventListener: connectMapListener(mapId, dispatch)
	};

	contextMap.set(mapId, context);

	//listening on custom event with specific name from map.
	eventBusElm.addEventListener(EVT_MAP_TO_WORKSPACE, context.mapEventListener);

	//one-time event, waiting for first event from map and receiving callback for seismic-to-map communication
	eventBusElm.addEventListener(EVT_CONNECT_MAP_TO_SEISMIC_BUS, context.connectEventListener);

	return true;
};

export const dispatchEventToMap = (eventName, args, mapId) => {
	LOGGING.debug(`NOC/MAP: map iframe: Sending event to map ${eventName}, ${stringify(args)}`);
	const context = contextMap.get(mapId);

	if (!context) {
		const e = new Error(`NOC/MAP: dispatchEventToMap could not find context by mapId ${mapId}. From [${eventName}]`);
		LOGGING.debug(stringify(e.stack));
		return;
	}

	const {workspaceEventReceiver} = context;
	if (workspaceEventReceiver) 
		workspaceEventReceiver(eventName, args);
    
};

export const destroyMapListeners = mapId => {
	LOGGING.debug(`NOC/MAP: destroyMapListeners with mapId: ${mapId}`);
	const context = contextMap.get(mapId);

	if (!context) 
		return;

	contextMap.delete(mapId);
	context.eventBusElm.removeEventListener(EVT_CONNECT_MAP_TO_SEISMIC_BUS, context.connectEventListener);
	context.eventBusElm.removeEventListener(EVT_MAP_TO_WORKSPACE, context.mapEventListener);
};