import {
	createActionConfig, 
	createNocElement,
	nocErrorBehavior,
	behaviorNocNotification
} from '@devsnc/library-opws-mapping-shared';

import MAP_CONSTANTS from '../map-shared/constants';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './mapContainer.scss';
import view from './mapContainerView';
import sendEventToMapEffect from './sendEventToMapEffect';
import mapEventReceivedEffect from './mapEventReceivedEffect';
import {mapInitialDataBehavior} from '../map-initial-data-behavior/mapInitialDataBehavior';
import {mapSettingsMenuBehavior} from '../map-settings-menu-behavior/mapSettingsMenuBehavior';
import {behaviorServiceHistory} from '../behavior-service-history';
import {actionTypes} from '@servicenow/ui-core';
import {computed as computedUrl} from './computedUrl';
import effectComputeRelatedTypes from './effectComputeRelatedTypes';
import {effectMapDisconnected} from './effectMapDisconnected';
import effectGetAlertsForCis from './effectGetAlertsForCis';
import effectGetAlertsForBS from './effectGetAlertsForBS';
import handleGetAlertsResponse from './handleGetAlertsResponse';
import effectGetMapChangeItems from './effectGetMapChangeItems';
import effectMapRelatedItemsRefresh from './effectMapRelatedItemsRefresh';
import effectGetIndicatorsForCis from './effectGetIndicatorsForCis';
import effectSidePanelBackAction from './effectSidePanelBackAction';
import effectOnCardSelection from './effectOnCardSelection';
import effectOnMapVisibilityChange from './effectOnMapVisibilityChanges';
import effectContainerToggleSidePanel from './effectContainerToggleSidePanel';

const {
	COMPONENT_DISCONNECTED
} = actionTypes;

/**
 * This component is a root container for the map of Application Service in Workspace.
 * 
 * ## Dev install & run
 * ```
 * mvn clean install
 * tectonic run develop --entry=./app.js
 * ```
 * @seismicElement map-container
 * @summary 
 */
createNocElement('map-container', {
	renderer: {
		type: snabbdom,
		view,
	},
	styles,

	initialState: {
		//first-time initial state. Used to hide components before the map is loaded for the first time.
		isInitializing: true,

		isBlocked: false,

		blockingState: {
			reason: null,
			url: null
		},

		//transitional laoding state (changes every time when map is loading)
		loadingState: MAP_CONSTANTS.LOADING_STATES.LOADING,

		setServiceNameCb: null,

		/**
		 * Getter function that receives severity code and returns severity color.
		 * This function provided by angular code and is called in seismic t oreuse fuctionality of factory.sncAccessibility. 
		 */
		severitiesGetter: null,

		/**
		 * Internal data of indicators to compare hash of sys_ids
		 * and eliminate duplicate calls when CI selected on map.
		 */
		indicatorsPanelData: {},

		mapSettings: {
			isOpen: false,
			model: null
		},
		mapInitialData: null,
		serviceSeverity: null,

		/**
		 * This data originates in `sn-opws-related-items-list`.
		 * It's dispatched from there with `OPWS_RELATED_ITEMS_LIST#REFRESH_REQUESTED` and is stored here 
		 * to trigger another dispatch of that type from map-container, when map data changed and arrived from eventBus.
		 * DO NOT try to pass this data to any child component, it is local to map-container only.
		 */ 
		relatedItemsRefreshParams: null,

		/**
		 * Cached response of topology changes cards.
		 * If topology timestamp wasn't updated, the cached data will be reused, skipping redundant server requests.
		 */
		topoChangesItemsCache: {},

		sysProperties: {}
	},

	properties: {

		/**
		 * A sys_id of the service for this map.
		 * @type {String}
		 */
		serviceId: {
			default: '',
			selectable: true
			//should be schema: sys_id, but workspace fails if I use it.
		},

		/**
		 * Defines whether the map is in Event Management mode (bringing severities layer).
		 * @type {Boolean}
		 */
		isEm: {
			default: true,
			schema: {
				type: 'boolean'
			}
		},

		/**
		 * Defines whether the map is in history mode.
		 * @type {Boolean}
		 */
		isHistory: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},

		/**
		 * Internal property.
		 * Stores data about current user id and preferences.
		 * @private
		 * @type {Object}
		 */
		userMeta: {
			selectable: true,
			default: {},
			schema: {
				type: 'object'
			}
		},

		/**
		 * Internal property.
		 * Stores direct fields of the service record that are displayed in the app.
		 * @private
		 * @type {Object}
		 */
		mapProperties: {
			default: null,
			selectable: true,
			schema: {
				oneOf: [
					{
						type: 'null'
					},
					{
						type: 'object'
					}
				]
			}
		},

		/**
		 * Internal computed property to resolve embedded map URL for the iframe.
		 * @private
		 * @type {String}
		 */
		url: {
			default: '',
			computed: computedUrl,
			schema: {
				type: 'string'
			}
		},

		/**
		 * Internal property.
		 * mapId is a unique ID of map application.
		 * For the same serviceId, there maybe multiple map application opened side-by-side in Workspace tabs.
		 * (Current map and history map)
		 * @private
		 * @type {String}
		 */
		mapId: {
			default: '',
			selectable: true,
			schema: {
				type: 'string'
			}
		},

		/**
		 * Internal property.
		 * Array of allowed item types on the contextual panel in the related items section.
		 * @private
		 * @type {array}
		 */
		relatedItemTypes: {
			selectable: true,
			default: [],
			schema: {
				type: 'array'
			}
		},

		/**
		 * Internal property.
		 * Mapping of ids of currently displayed cards on the contextual panel in the related items section.
		 * Mapped by itemTypes
		 * @private
		 * @type {object}
		 */
		relatedItemIds: {
			selectable: true,
			default: {},
			schema: {
				type: 'object'
			}
		},

		/**
		 * Internal property.
		 * Separate flag to set when cards are in loading state and the loader should be shown.
		 * @private
		 * @type {boolean}
		 */
		cardsAreLoading: {
			selectable: true,
			default: false,
			schema: {
				type: 'boolean'
			}
		},

		/**
		 * Internal property.
		 * Response objects that contains:
		 * `items` - array of item DTOs.
		 * `lastUpdated` - UNIX format tiemstamp of last updated time for the items.
		 * `totalRecords` - total number of items (in all pages).
		 *  `RequestTime` formated time for server.
		 * `filterApplied` - {type, ...} object that describes type of the filter that applied on the items list, and additional attributes, depending on the type.
		 * Allowed types in filterApplied are described in map-shared/constants.CONTEXT_SELECTION_STATES
		 * @private
		 * @type {object}
		 */
		relatedItemsResponse: {
			selectable: true,
			default: {},
			schema: {
				type: 'object'
			}
		},

		/**
		 * Array of filtered items that are displayed on the side panel, before they are formatted and sorted for display.
		 * The sorting here is always by the timestamp ASC.
		 * @private
		 * @type {array}
		 */
		filteredItems: {
			selectable: true,
			default : [],
			schema: {
				type: 'array'
			}
		},
		/**
		 * Id of the selected card, or '' if not selected.
		 * @type {string}
		 */
		selectedCardId: {
			default: '',
			selectable: true,
			schema: {
				type: 'string'
			}
		}
	},

	actionHandlers: {
		[sendEventToMapEffect.ACTION_TYPES.NOC_EVENT_TO_MAP_SENT]:
			createActionConfig(sendEventToMapEffect.effectHandlers.sendEventToMapHandler),

		[effectComputeRelatedTypes.ACTION_TYPES.MAP_SETTINGS_SET]: 
			createActionConfig(effectComputeRelatedTypes.effectHandlers.effectComputeRelatedTypes),

		[mapEventReceivedEffect.ACTION_TYPES.NOC_EVENT_FROM_MAP_ARRIVED]:
			createActionConfig(mapEventReceivedEffect.effectHandlers.mapEventReceivedEffect),

		[effectMapRelatedItemsRefresh.ACTION_TYPES.REFRESH_REQUESTED]:
			createActionConfig(effectMapRelatedItemsRefresh.effectHandlers.effectMapRelatedItemsRefresh),

		[effectGetAlertsForCis.ACTION_TYPES.MAP_GET_ALERTS_FOR_CIS_REQUEST]:
			createActionConfig(effectGetAlertsForCis.effectHandlers.getMapAlertsForCisEffectHandler),

		[effectGetAlertsForCis.ACTION_TYPES.MAP_GET_ALERTS_FOR_CIS_REQUEST_SUCCEEDED]:
			createActionConfig(handleGetAlertsResponse.effectHandlers.handleGetMapAlertsResponse),

		[effectGetAlertsForCis.ACTION_TYPES.MAP_GET_ALERTS_FOR_CIS_REQUEST_FAILED]:
			createActionConfig(handleGetAlertsResponse.effectHandlers.handleGetMapAlertsFailed),

		[effectGetAlertsForBS.ACTION_TYPES.MAP_GET_ALERTS_FOR_BS_REQUEST]:
			createActionConfig(effectGetAlertsForBS.effectHandlers.getMapAlertsForBSeffectHandler),

		[effectGetAlertsForBS.ACTION_TYPES.MAP_GET_ALERTS_FOR_BS_REQUEST_SUCCEEDED]:
			createActionConfig(handleGetAlertsResponse.effectHandlers.handleGetMapAlertsResponse),

		[effectGetAlertsForCis.ACTION_TYPES.MAP_GET_ALERTS_FOR_BS_REQUEST_FAILED]:
			createActionConfig(handleGetAlertsResponse.effectHandlers.handleGetMapAlertsFailed),

		[effectGetMapChangeItems.ACTION_TYPES.GET_HISTORY_CHANGES_REQUEST]:
			createActionConfig(effectGetMapChangeItems.effectHandlers.getHistoryChanges),

		[effectGetMapChangeItems.ACTION_TYPES.GET_HISTORY_CHANGES_REQUEST_SUCCEEDED]:
			createActionConfig(effectGetMapChangeItems.effectHandlers.historyChangesResponse),

		[effectGetMapChangeItems.ACTION_TYPES.GET_HISTORY_CHANGES_REQUEST_FAILED]:
			createActionConfig(effectGetMapChangeItems.effectHandlers.historyChangesFailure),

		[effectGetIndicatorsForCis.ACTION_TYPES.MAP_GET_INDICATORS_FOR_CIS_REQUEST_SUCCEEDED]:
			createActionConfig(effectGetIndicatorsForCis.effectHandlers.handleGetMapIndicatorsForCisResponse),

		[effectSidePanelBackAction.ACTION_TYPES.RELATED_ITEMS_BACK]:
			createActionConfig(effectSidePanelBackAction.effectHandlers.relatedItemsBack),

		[effectSidePanelBackAction.ACTION_TYPES.PROPERTIES_BACK]:
			createActionConfig(effectSidePanelBackAction.effectHandlers.propertiesBack),

		[COMPONENT_DISCONNECTED]: 
			createActionConfig(effectMapDisconnected),

		[effectOnCardSelection.ACTION_TYPES.CARD_SELECTED]:
		createActionConfig(effectOnCardSelection.effectHandlers.onCardSelection),

		[effectOnMapVisibilityChange.ACTION_TYPES.MAP_VISIBILITY_CHANGE]:
			createActionConfig(effectOnMapVisibilityChange.effectHandlers.onMapVisibilityChange),

		[effectContainerToggleSidePanel.ACTION_TYPES.TOGGLE_SIDE_PANEL]:
			createActionConfig(effectContainerToggleSidePanel.effectHandlers.toggleSidePanel)
	},

	behaviors: [
		mapSettingsMenuBehavior,
		mapInitialDataBehavior,
		nocErrorBehavior,
		behaviorNocNotification,
		behaviorServiceHistory
	]
});

