import {t} from 'sn-translate';
import {get as _get, truncate as _truncate} from 'lodash';
import moment from 'moment';
import MAP_CONSTANTS from '../map-shared/constants';
import {isHistoryMap} from '../behavior-service-history/timelineFunctors';
import '@servicenow/now-label-value';
import '@servicenow/now-image';
import '@servicenow/now-heading';
import '@devsnc/library-opws-mapping-shared'; //noc-element-with-tooltip

const CI_TYPE_ICON_SZ = 24; //px, aligned with now-icon size='lg'
const LABEL_TRUNCATION_LIMIT = 50;
const LABEL_TRUNCATION_LIMIT_SHORT = 25;

const LABEL_PROPERTIES_HEADER = t('Properties');
const LABEL_RELATED_ITEMS_HEADER = t('Related items');

const LABEL_PROPERTIES_HISTORY_HEADER = t('Properties history');
const LABEL_RELATED_ITEMS_HISTORY_HEADER = t('Change history');

const HOST_LABEL_FORMAT = t(`on ${'HOST_LABEL'}`);
const CI_AND_HOST_LABEL_FORMAT = t(`${'CI_LABEL'} on ${'HOST_LABEL'}`);
const LABEL_NAME = t('Name');

const TITLE_RELATED_ITEMS = t(`Related items for ${'CI_TYPE'}`);
const TITLE_CHANGES = t(`Change history for ${'CI_TYPE'}`);
const TITLE_PROPERTIES = t(`Properties of ${'CI_TYPE'}`);
const TITLE_PROPERTIES_HISTORY_OF = t(`Properties history for ${'CI_TYPE'}`);

const TITLE_SINGLE_CHANGE_SELECTED = t('1 change selected');
const TITLE_CHANGES_SELECTED = t(`${'COUNT'} changes selected`); 
const LABEL_ON = t('On');
const LABEL_STARTING = t('Starting');
const LABEL_ENDING = t('Ending');

const getLabelJsx = ({label, onClick, className = '', tooltip = ''}) => {
	const jsx = onClick ? 

		<now-text-link
			className={className}
			label={label}
			on-click={onClick}
			href='javascript:void(0)'
			variant='secondary'/> : 

		<label className={className}>{label}</label>;

	return tooltip ? (
		<noc-element-with-tooltip tooltip={tooltip}>
			{jsx}
		</noc-element-with-tooltip>
	) :
		jsx;
};

const openCiFormHandler = ({serviceId, sections = [], sectionId, dispatch}) => {
	const section = sections.find(section => section.id === sectionId);

	if (!section)
		return null;

	//Endpoint/entry-point types don't have user-friendly forms. Preventing links.
	if (isEndpoint({section}))
		return null;

	const tableName = _get(section, 'data.Class.value', '');

	const {sys_id: sysId} = section;

	if (!tableName || !sysId) 
		return null;

	return () => 	
		dispatch('ITEM_SELECTED', {
			table: tableName,
			sys_id: sysId,
			parent_table: 'cmdb_ci_service_discovered',
			parent_sys_id: serviceId
		});
};

const formatHostLabelText = (ciLabel, hostLabel) => {
	if (!hostLabel)
		return ' ';

	if (!ciLabel)
		return hostLabel;
		
	return HOST_LABEL_FORMAT.replace('HOST_LABEL', hostLabel);
};

const getCiRelatedHeader = ({serviceId, mapProperties, dispatch}) => {
	if (!mapProperties)
		return null;

	const {
		icon,
		name: hostLabel,
		type: ciLabel,
		sections = []
	} = mapProperties;

	if (!ciLabel && !hostLabel)
		return null;

	const hostLabelFormatted = formatHostLabelText(ciLabel, hostLabel);

	const ciTypeAlt = getCiType(mapProperties);
	const iconImage = icon ?
		<now-image 
			className='now-m-inline-end--sm'
			src={icon} 
			alt={ciTypeAlt} 
			height={CI_TYPE_ICON_SZ} 
			width={CI_TYPE_ICON_SZ} 
			fit='contain' 
			position='left'/> : 
		null;

	const ciFormHandler = openCiFormHandler({
		serviceId, 
		sections, 
		sectionId: 'sa_ci_properties_Application', 
		dispatch
	});
	
	const hostFormHandler = openCiFormHandler({
		serviceId, 
		sections, 
		sectionId: 'sa_ci_properties_Server', 
		dispatch
	});

	const truncatedCiLabel = _truncate(ciLabel || '', LABEL_TRUNCATION_LIMIT);
	const truncatedHostLabel = _truncate(hostLabelFormatted || '', LABEL_TRUNCATION_LIMIT);
	
	const ciLabelJsx = ciLabel ? 
		getLabelJsx({
			label: truncatedCiLabel,
			tooltip: (ciLabel === truncatedCiLabel) ? '' : ciLabel,
			onClick: ciFormHandler, 
			className: 'now-align -block-start font-md'
		}) : 
		null;

	const hostLabelJsx = getLabelJsx({
		label: truncatedHostLabel,
		tooltip: (hostLabelFormatted === truncatedHostLabel) ? '' : hostLabelFormatted,
		onClick: hostFormHandler, 
		className: ciLabel ? 'now-align -block-end font-sm' : 'now-align -block-center font-md'
	});

	return {
		iconImage,
		ciLabelJsx,
		hostLabelJsx
	};
};

/**
 * Sub-case of secondary title for the cards panel, when timeline applies the filter.
 * @param {Number} count - total number of cards to appear on the panel.
 */
const getSelectedChangesSummary = count => 
	(count === 0) ? '' : 
		(count === 1) ? 
			TITLE_SINGLE_CHANGE_SELECTED : 
			TITLE_CHANGES_SELECTED.replace('COUNT', count);

/**
 * Secondary title appears only when the panel has filter applied.
 * This function returns text for secondary title for the cards panel.
 * @param {Object} 
 * `filterType` - the type of the filtering that is applied on the panel.
 * `mapHistoryState` - current history state of the map.
 * `ciType` - currently selected CI type, or null
 * @returns {string} - translated text for the secondary title.
 */
const getItemsSecondaryTitle = ({filterType, mapHistoryState, ciType, items}) => {
	if (!filterType)
		return '';

	if (filterType === MAP_CONSTANTS.CONTEXT_SELECTION_STATES.TIMELINE)
		return getSelectedChangesSummary(items.length);

	if (ciType) {
		return (mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT ?
			TITLE_RELATED_ITEMS : 
			TITLE_CHANGES)
			.replace('CI_TYPE', ciType);
	}

	return (mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT ?
		LABEL_RELATED_ITEMS_HEADER : 
		LABEL_RELATED_ITEMS_HISTORY_HEADER);
};

/**
 * Secondary title appears only when the panel has filter applied.
 * This function returns text for secondary title for the map roperties panel.
 * @param {object}
 * `filterType` - the type of the filtering that is applied on the panel.
 * Only to return no title if the filtering type is not 'MAP'.
 * 
 * `mapHistoryState` - current history state of the map.
 * `ciType` - currently selected CI type, or null
 * @returns {string} - translated text for the secondary title.
 */
const getPropertiesSecondaryTitle = ({filterType, mapHistoryState, ciType}) => {
	if (filterType !== MAP_CONSTANTS.CONTEXT_SELECTION_STATES.MAP)
		return '';

	//cannot resolve ci type, return generic title
	if (!ciType) {
		return mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT ? LABEL_PROPERTIES_HEADER : 
			isHistoryMap(mapHistoryState) ? LABEL_PROPERTIES_HISTORY_HEADER : '';
	}

	const typedTitle = mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT ? TITLE_PROPERTIES : 
		isHistoryMap(mapHistoryState) ? TITLE_PROPERTIES_HISTORY_OF : '';

	return typedTitle.replace('CI_TYPE', ciType);
};

/**
 * Returns text for the CI label.
 * Resolves app CI and host CI labels for all possible combinations, and returns the translated label.
 * @param {*} mapProperties 
 * @returns {String}
 */
const buildCiLabel = (mapProperties, truncate = true) => {
	const truncator = truncate ? _truncate : text => text;

	if (isEndpoint({mapProperties}))
		return '';

	const {
		name: hostLabel,
		type: ciLabel
	} = mapProperties || {};

	if (!ciLabel && !hostLabel)
		return '';

	if (!hostLabel)
		return truncator(ciLabel || '', LABEL_TRUNCATION_LIMIT);

	if (!ciLabel)
		return truncator(hostLabel || '', LABEL_TRUNCATION_LIMIT);
		
	return CI_AND_HOST_LABEL_FORMAT
		.replace('CI_LABEL', truncator(ciLabel || '', LABEL_TRUNCATION_LIMIT_SHORT))
		.replace('HOST_LABEL', truncator(hostLabel || '', LABEL_TRUNCATION_LIMIT_SHORT));
};

const getHistoryRangeTimestamps = ({displayRange, items, filterType}) => {
	//when in properties panel OR if no filtering applied: use the timeline display range for dates.
	//otherwise, the range is resolved from the filtered change cards. 
	const useDisplayRange = (!items || filterType !== MAP_CONSTANTS.CONTEXT_SELECTION_STATES.TIMELINE);

	if (useDisplayRange)
		return displayRange;
	
	//no dates displayed if single change is filtered.
	if (items.length === 1)
		return {};

	//having >1 items: dispalying range for these items.
	//assumption: items are sorted by time ASC. 
	return {
		start: items[0].time,
		end: items[items.length - 1].time
	};
};

/**
 * Returns `items` array for label-value-stacked or label-value-tabbed use.
 * The array contains one or two formatted dates for the current state of the panel on history map only.
 * @param {*} param0
 *  `displayRange` - current display range of the timeline.
 *  `items` - currently displayed card items on the panel, or null.
 *  `filterType` - type of filtering applied o nthe panel.
 * @returns {Array}
 */
const getHistoryChangesDates = ({displayRange = {}, items, filterType}) => {
	//assuming (mapHistoryState !== MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT)
	const timestamps = getHistoryRangeTimestamps({displayRange, items, filterType});

	//no dates displayed if single change is filtered.
	if (!timestamps.start)
		return [];

	const {
		start,
		end = timestamps.start
	} = timestamps;

	//If timeline changes selected, then showing approximate date without the time part.
	const dateFormat = (filterType === MAP_CONSTANTS.CONTEXT_SELECTION_STATES.TIMELINE) ? 'LL' : 'LLL';

	const startDate = moment(start).format(dateFormat);
	const endDate = moment(end).format(dateFormat);

	//this flag resolves when starting and ending dates are equal and no need to present the same date twice.
	const needBoth = startDate !== endDate;

	const itemsModel = [
		{
			label: needBoth ? LABEL_STARTING : LABEL_ON,
			value: {
				type: 'string',
				value: startDate
			}
		},
		
		...(needBoth ? [
			{
				label: LABEL_ENDING,
				value: {
					type: 'string',
					value: endDate
				}
			}
		] : [])
		
	];

	return itemsModel;
};

/**
 * Resolves JSX for the label-value pairs that should appear under the secondary title on the panel header.
 * @param {*} param0 
 *  `items`
 *  `filterType`
 *  `mapProperties`
 *  `displayRange`
 * @returns {JSX}
 */
const getHistoryHeaderAttrs = ({items, filterType, mapProperties, displayRange}) => {
	const dates = getHistoryChangesDates({displayRange, items, filterType});

	const name = (filterType === MAP_CONSTANTS.CONTEXT_SELECTION_STATES.MAP) ? 
		buildCiLabel(mapProperties) : 
		'';

	//using non-truncated version of formatted CI label to set the tooltip
	const tooltip = buildCiLabel(mapProperties, false);

	const nameItemWrapped = (!name) ? [] : (name === tooltip) ? [
		{
			label: LABEL_NAME,
			value: {
				type: 'string',
				value: name
			}
		}
	] : [
		{
			label: LABEL_NAME,
			value: {
				type: 'jsx',
				value: (
					<noc-element-with-tooltip tooltip={tooltip}>
						<span className='now-align' aria-label={tooltip}>{name}</span>
					</noc-element-with-tooltip>
				)
			}
		}
	];

	const itemsModel = [
		...nameItemWrapped,
		...dates
	];

	const jsx = itemsModel.length === 0 ? null : (
		<now-label-value-tabbed
			className='history-header-attrs flex-full-width'
			size='md'
			items={itemsModel}
		/>
	);

	return jsx;
};

/**
 * Test if the selected element on the map is of type Endpoint/Entrypoint.
 * @param {*} param0 Only one of the two parameters is required.
 * `section` - exact section data from the current mapProeprties model.
 * `mapProperties` - the entire mapProperties model. If `section` is not given, `mapProperties` will be used to address the first section.
 */
export const isEndpoint = ({section, mapProperties}) => {
	const _section = (section || _get(mapProperties || {}, 'sections[0]')) || {};
	return (_get(_section, 'data.Class.value') || '').startsWith('cmdb_ci_endpoint');
};

/**
 * Returns the label text for the selected CI type, or an empty string for types that shouldn't display CI type label.
 * @param {*} mapProperties 
 * @returns {String}
 */
const getCiType = mapProperties => {
	if (isEndpoint({mapProperties}))
		return '';

	return _get(mapProperties, 'sections[0].data[\'CI Type\'].value') || '';
};

export const getElementType = mapProperties => getCiType(mapProperties) ||
		_get(mapProperties, 'sections[0].data[\'Endpoint Type\'].value') || 
		'';

/**
 * Returns transalted text for the side panel primary title, or an empty string when no title should appear.
 * @param {*} param0 
 *  `isFilterActive`
 *  `isPropertiesPanel`
 *  `mapHistoryState`
 */
export const getSidePanelTitle = ({isFilterActive, isPropertiesPanel, mapHistoryState}) => {
	if (isFilterActive)
		return '';

	if (isPropertiesPanel) {
		return mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT ? LABEL_PROPERTIES_HEADER :
			isHistoryMap(mapHistoryState) ? LABEL_PROPERTIES_HISTORY_HEADER : ''; 
	}
	//related items panel
	return mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT ? LABEL_RELATED_ITEMS_HEADER :
		LABEL_RELATED_ITEMS_HISTORY_HEADER;
};

/**
 * Returns JSX for the side panel header on the map in all its possible modes.
 * @param {*} params 
 * 	`serviceId` {String}
 *  `items` {Array} - array of displayed card items, sorted in timeline order (timestamp ASC)
 *  `mapProperties` {Object}
 *  `mapHistoryState` {String}
 *  `dispatch` {Function}
 *  `filterApplied` {String}
 *  `displayRange` {{start: Number, end: Number}}
 */
export const getSidePanelHeaderJsx = ({serviceId, items, mapProperties, mapHistoryState, dispatch, filterApplied, displayRange}) => {
	const filterType = (filterApplied && filterApplied.type) || null;
	const ciType = getCiType(mapProperties);

	const ciRelatedHeader = (filterType === MAP_CONSTANTS.CONTEXT_SELECTION_STATES.MAP && 
		mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT) ? 
		getCiRelatedHeader({serviceId, mapProperties, dispatch}) : 
		null;

	const {
		iconImage = null,
		ciLabelJsx = null,
		hostLabelJsx = null
	} = ciRelatedHeader || {};

	const ciRelatedHeaderJsx = ciRelatedHeader ? (
		<div className='ci-related-header now-align -block-end'>
			{iconImage}
			<div className='labels-container'>
				{ciLabelJsx}
				{hostLabelJsx}
			</div>
		</div>
	) : null;

	const isPropertiesPanel = !items;

	const secondaryTitle = isPropertiesPanel ? 
		getPropertiesSecondaryTitle({filterType, mapHistoryState, ciType}) :
		getItemsSecondaryTitle({mapHistoryState, ciType, filterType, items});

	const secondaryTitleJsx = secondaryTitle ? (
		<now-heading 
			className='secondary-title now-align -block-start'
			label={secondaryTitle} 
			level='2' 
			variant='title-primary'/>
	) : null;
	
	const historyHeaderAttrsJsx = (mapHistoryState !== MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT) ? 
		getHistoryHeaderAttrs({items, filterType, mapProperties, displayRange}) : 
		null;

	return (secondaryTitleJsx || historyHeaderAttrsJsx || ciRelatedHeaderJsx) ? (
		<div className='now-align flex-column -block-start flex-full-width'>
			{secondaryTitleJsx}
			{historyHeaderAttrsJsx}
			{ciRelatedHeaderJsx}
		</div>
	) : null;
};