import {get as _get} from 'lodash';

const openIndicatorForm = coeffects => {
	const {
		action: {payload: {id, typeId}},
		dispatch,
		properties: {serviceId, itemIds},
	} = coeffects;

	const table = _get(itemIds[typeId], 'urlData.params.sysparm_table') || '';

	dispatch('ITEM_SELECTED', {
		table,
		sys_id: id,
		parent_table: 'cmdb_ci_service_discovered',
		parent_sys_id: serviceId
	});
};

const effectHandlers = {
	openIndicatorForm
};

export default {
	effectHandlers
};