import {t} from 'sn-translate';
import '@servicenow/now-record-common';
import './actions/sn-map-properties';
import './actions/mapCardTypes';
import '../sn-map-related-items-list';
import {getSidePanelTitle} from '../map-side-panel/getSidePanelHeader';

const LABEL_SIDE_PANEL = t('Side panel');

const sidePanelPayload = {
	sidePanelStatePropertyName: 'opened'
};

const getPropertiesTab = (mapComponentId, mapHistoryState) => ({
	actionComponent: 'sn-map-properties',
	actionAttributes: {
		mapProperties: `@${mapComponentId}/mapProperties`,
		serviceId: `@${mapComponentId}/serviceId`,
		displayRange: `@${mapComponentId}/timelineData.displayRange`,
		mapHistoryState
	},
	order: 0,
	assignmentId: `${mapComponentId}-sn-map-properties`, //important for unique componentId
	name: 'sn-map-properties',
	tooltip: getSidePanelTitle({isFilterActive: false, isPropertiesPanel: true, mapHistoryState}),
	icon: 'circle-info-outline'
});

const getRelatedItemsTab = (mapComponentId, mapHistoryState) => {
	const headerTitle = getSidePanelTitle({isFilterActive: false, isPropertiesPanel: false, mapHistoryState});

	const actionAttributes = {
		componentId: `${mapComponentId}_related_items_list`,
		isLoading: `@${mapComponentId}/cardsAreLoading`,
		serviceId: `@${mapComponentId}/serviceId`,
		itemTypes: `@${mapComponentId}/relatedItemTypes`,
		itemIds: `@${mapComponentId}/relatedItemIds`,
		itemsResponse: `@${mapComponentId}/relatedItemsResponse`,
		filteredItems: `@${mapComponentId}/filteredItems`,
		mapProperties: `@${mapComponentId}/mapProperties`,
		selectedCardId: `@${mapComponentId}/selectedCardId`,
		displayRange: `@${mapComponentId}/timelineData.displayRange`,
		topologyChangesRange: `@${mapComponentId}/userMeta.topologyChangesRange`,
		mapHistoryState
	};

	return {
		actionComponent: 'sn-map-related-items-list',
		actionAttributes,
		order: 1,
		assignmentId: `${mapComponentId}-sn-map-related-items-list`, //important for unique componentId
		name: 'sn-map-related-items-list',
		tooltip: headerTitle,
		icon: 'list-fill'
	};
};

export default state => {
	const {
		opened, 
		mapComponentId,
		mapHistoryState,
		isLoading,
		isBlocked
	} = state.properties;
	
	//not creating related items tab until the map is fully loaded
	const actions = isLoading || isBlocked ? [
		getPropertiesTab(mapComponentId, mapHistoryState)
	] : [
		getPropertiesTab(mapComponentId, mapHistoryState),
		getRelatedItemsTab(mapComponentId, mapHistoryState)
	];

	const wrapperClass = {
		'map-side-panel': true,
		'is-opened': opened
	};

	return (
		<aside class={wrapperClass} aria-label={LABEL_SIDE_PANEL}>
			<now-record-common-sidebar 
				actions={actions} 
				append-to-payload={sidePanelPayload}
				manage-opened
				opened={opened}/>
		</aside>
	);
};
