import {markCisOnMap} from './effectMarkCisInMap';
import {get as _get} from 'lodash';

const getSelectedCis = coeffects => {
	const {
		dispatch, 
		action: {
			payload: {
				isSelected,
				item = {}
			}
		}
	} = coeffects;
	
	const ciId = _get(item, 'ciField.value', '');

	const cmdbCiId = isSelected && ciId ? [ciId] : [];
	markCisOnMap(dispatch, cmdbCiId);
};

const effectHandlers = {
	getSelectedCis
};

export default {
	effectHandlers
};