import {
	createNocElement, 
	createActionConfig, 
	effectToggleSidePanel
} from '@devsnc/library-opws-mapping-shared';

import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './mapSidePanelView';
import styles from './mapSidePanel.scss';

createNocElement('map-side-panel', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		isLoading: {
			default: true,
			schema: {
				type: 'boolean'
			}
		},

		isBlocked: {
			default: true,
			schema: {
				type: 'boolean'
			}
		},

		mapComponentId: {
			default: ''
		},

		opened: {
			default: true,
			schema: {
				type: 'boolean'
			}
		},

		mapHistoryState: {
			default: null
		}
	},

	actionHandlers: {
		[effectToggleSidePanel.ACTION_TYPES.TOGGLE_SIDE_PANEL]:
			createActionConfig(effectToggleSidePanel.effectHandlers.toggleSidePanel, false)
	}
});
