import {createNocElement, createActionConfig} from '@devsnc/library-opws-mapping-shared';
import {actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import MAP_CONSTANTS from '../map-shared/constants';
import toggleCheckedEffect from './toggleCheckedEffect';
import effectDropdownItemSelected from './effectDropdownItemSelected';
import styles from './mapHeaderPanel.scss';
import view from './mapHeaderPanelView';

const {COMPONENT_DOM_READY} = actionTypes;

createNocElement('map-header-panel', {
	renderer: {
		type: snabbdom,
		view,
	},

	styles,

	properties: {
		isLoading: {
			default: true,
			schema: {
				type: 'boolean'
			}
		},

		isBlocked: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		
		serviceId: {
			default: ''
		},

		/**
		 * mapId is a unique ID of map application.
		 * For the same serviceId, there maybe multiple map application opened side-by-side in Workspace tabs.
		 * (Current map and history map)
		 */
		mapId: {
			default: ''
		},

		isHistory: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},

		isEm: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},

		serviceSeverity: {
			default: null
		},

		mapInitialData: {
			default: null
		},

		mapSettings: {
			default: null
		},

		notifications: {
			default: []
		}
	},

	actionHandlers: {
		[toggleCheckedEffect.ACTION_TYPES['NOW_TOGGLE#CHECKED_SET']]:
			createActionConfig(toggleCheckedEffect.effectHandlers.toggleCheckedEffect),

		[effectDropdownItemSelected.ACTION_TYPES.ITEM_CLICKED]:
			createActionConfig(effectDropdownItemSelected.effectHandlers.onDropdownItemClicked),

		[COMPONENT_DOM_READY]: ({dispatch}) => dispatch(MAP_CONSTANTS.SN_MAP_CONNECTED)
	}
});