import {getIndicatorsSection} from '../map-header-panel/settingsMenuSections';

const MAP_EVENT_NAMES = {
	//this event sends object wit hsw-options-popover controls by reference.
	//this object has original AngularJs functions to execute actions (toggles/buttons)
	EVT_WS_OPTIONS_POPOVER_CONTROLS_TRANSFER: 'EVT_WS_OPTIONS_POPOVER_CONTROLS_TRANSFER',

	//Change in any indicator toggle
	'sa.mashupStateChanged':'sa.mashupStateChanged',

	//host view toggle
	'sa.hostViewStateChanged': 'sa.hostViewStateChanged',

	//Group CIs toggle
	'sa.autoGroupingViewStateChanged': 'sa.autoGroupingViewStateChanged',

	//Show Traffic-based toggle
	'sa.trafficBaseDiscoveryStateChanged': 'sa.trafficBaseDiscoveryStateChanged',

	//Spanning tree view toggle
	'sa.shortestPathViewStateChanged': 'sa.shortestPathViewStateChanged'
};

const ACTION_TYPES = {
	NOC_SETTINGS_MODIFIED_EVENT: 'NOC_SETTINGS_MODIFIED_EVENT', //when event with mapSettings arrvies from map
	MAP_SETTINGS_SET: 'MAP_SETTINGS_MENU_BEHAVIOR#MAP_SETTINGS_SET' //when 'mapSettings' property has been set on map-container.
};

const setIndicatorIds = indicatorIds => {
	if (!indicatorIds)
		return;
	
	const indicatorsSection = getIndicatorsSection();

	if (!indicatorsSection) 
		return;

	indicatorsSection.keys = indicatorIds;
};

const mapSettingsModifiedEffect = coeffects => {
	const {
		state: {mapSettings},
		updateState,
		dispatch, 
		action
	} = coeffects;

	const {name, args} = action.payload;
	let newMapSettings = mapSettings;

	if (name === MAP_EVENT_NAMES.EVT_WS_OPTIONS_POPOVER_CONTROLS_TRANSFER) {
		newMapSettings = {
			...mapSettings,
			model: {...args[0]}
		};

		setIndicatorIds(newMapSettings.model.indicatorsIds);

	} else if (MAP_EVENT_NAMES[name]) {
		//bump state to re-render settings form.
		newMapSettings = {...mapSettings};
	}

	updateState({mapSettings: newMapSettings});

	dispatch(() => ({type: ACTION_TYPES.MAP_SETTINGS_SET}));
};


const effectHandlers = {
	mapSettingsModifiedEffect
};

export default {
	MAP_EVENT_NAMES,
	ACTION_TYPES,
	effectHandlers
};