import {createCardComponent, generateViewFn} from '@devsnc/library-opws-mapping-shared';
import {getOutageCardContentJsx as getCardContentJsx} from './getOutageCardContentJsx';
import {CARD_COMPONENTS, INDICATOR_IDS, ACTION_TYPES} from '../map-shared/indicatorTypes';
import styles from './snMapOutageCard.scss';

const view = generateViewFn({
	getCardContentJsx,
	openFormActionType: ACTION_TYPES.OPEN_INDICATOR_FORM_INVOKED,
	cardSelectedActionType: ACTION_TYPES.MAP_INDICATOR_CARD_SELECTED,
	headingFieldName: 'taskNumber',
	descriptionFieldName: 'cmdbCi'
});

/**
 * Seismic component `sn-map-outages-card`.
 * Card for map topology changes to show on side panel on nocpit and on map.
 **/
createCardComponent({
	componentName: CARD_COMPONENTS[INDICATOR_IDS.MAP_OUTAGES],
	styles,
	view
});