const ACTION_TYPES = {
	MAP_TOGGLE_SETTINGS_VIEW: 'MAP_TOGGLE_SETTINGS_VIEW'
};

const mapToggleSettingsEffect = coeffects => {
	const {
		state: {
			mapSettings
		},
		updateState,
		action: {
			payload
		}
	} = coeffects;

	const {isOpen = false} = payload;

	updateState({
		mapSettings: {
			...mapSettings,
			isOpen
		}
	});
};

const mapSettingsModalClosedEffect = coeffects => {
	const {
		state: {
			mapSettings
		},
		properties: {
			mapId
		},
		action: {
			payload
		},
		updateState
	} = coeffects;

	if (payload.mapId !== mapId)
		return;

	updateState({
		mapSettings: {
			...mapSettings,
			isOpen: payload.value
		}
	});
};

const effectHandlers = {
	mapToggleSettingsEffect,
	mapSettingsModalClosedEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};