import {snOpwsAlertCardActionTypes} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
	OPEN_ALERT_FORM_INVOKED: snOpwsAlertCardActionTypes.OPEN_ALERT_FORM_INVOKED
};

const openAlertForm = coeffects => {
	const {
		action: {payload},
		dispatch
	} = coeffects;

	const {
		serviceId,
		alertId
	} = payload;

	dispatch('ITEM_SELECTED', {
		table: 'em_alert',
		sys_id: alertId,
		parent_table: 'cmdb_ci_service_discovered',
		parent_sys_id: serviceId
	});
};

const effectHandlers = {
	openAlertForm
};

export default {
	ACTION_TYPES,
	effectHandlers
};