import constants from '../map-shared/constants';
import {dispatchEventToMap} from './eventBus';

const ACTION_TYPES = {
	NOC_EVENT_TO_MAP_SENT: constants.NOC_EVENT_TO_MAP_SENT,

	//events that are sent from siesmic to the angular map

	//mark elements on map by CI/element id.
	'sa.markNodes': 'sa.markNodes',

	//reset current selections on the map
	'sa.clearAllSelections': 'sa.clearAllSelections',

	//sets a schedule to fit map to screen after next rendering.
	EVT_SCHEDULE_FIT_TO_SCREEN: 'snc.scheduleFitToScreen',

	//request to re-render the map.
	EVT_LAYOUT_REAPPLIED: 'snc.layoutReapplied',
};

const sendEventToMapHandler = coeffects => {
	const {
		action: {
			payload: {
				name,
				args
			}
		}, 
		properties: {
			mapId
		}
	} = coeffects;
    
	dispatchEventToMap(name, args, mapId);
};

const effectHandlers = {
	sendEventToMapHandler
};

export default {
	ACTION_TYPES,
	effectHandlers
};