import {LOGGING, CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {INDICATOR_IDS_HASH} from '../map-shared/indicatorTypes';
import effectGetIndicatorsForCis from './effectGetIndicatorsForCis';

const {RELATED_ITEMS_PAGE_SIZE} = CONSTANTS.DEFAULTS;
const TASK_CI = 'task_ci';

const getIndicatorParams = (params, selectedType) => {
	const {
		table: selectedTable, 
		ciField: selectedCiField, 
		groupBy: selectedGroupBy
	} = selectedType;

	const {
		sysparm_cmdb_ci_field: ciField = '',
		sysparm_table: table = '',
		sysparm_query: query = ''
	} = params;

	if (table === TASK_CI) {
		return {
			query,
			ciField: selectedCiField,
			table: selectedTable,
			groupBy: selectedGroupBy
		};
	}

	return {
		ciField,
		query,
		table,
		groupBy: selectedGroupBy
	};
};

export const getGraphQlMapIndicatorsEffect = coeffects   => {
	const {
		properties: {
			relatedItemIds
		},
		action: {
			payload: {
				selectedType
			}
		}
	} = coeffects;

	if (selectedType && !INDICATOR_IDS_HASH[selectedType.id]) {
		LOGGING.warn(`createIndicatorsGqlEffect:  called and did nothing,
        because expected typeId from ${INDICATOR_IDS_HASH}, but got ${selectedType.id}`);
		return;
	}

	if(INDICATOR_IDS_HASH[selectedType.id]) {
		const {
			attributes, 
			id: indicatorId,
		} = selectedType;
        
		const relatedItem = relatedItemIds[indicatorId];
		const {urlData, sysIds} = relatedItem;
		const {params} = urlData;

		const {ciField, query, table, groupBy} = getIndicatorParams(params, selectedType);

		const sysIdQuery = `${ciField}IN${sysIds.join(',')}`;
		const newQueryArr = [
			sysIdQuery,
			query
		];

		const queryConditions = newQueryArr.join('^');

		const args = {
			table,
			queryConditions,
			attributes,
			groupBy
		};

		const qlQuery = getDynamicGraphQl(args);
		return createIndicatorsGraphQl(qlQuery);
	}
};

const buildGqlForAllAttributes = attributes => 
	attributes.map(attribute => `${attribute} {
                    label
                    displayValue 
                    value
                 }`);

const getDynamicGraphQl = args => {
	const {
		attributes,
		queryConditions,
		table,
		groupBy
	} = args;

	const gQlAttributes = buildGqlForAllAttributes(attributes);
	const allAttrStr = `_results { 
                                    ${gQlAttributes.join('')}
                                 }
                            `;

	return `query getMapIndicatorsForCis {
	        GlideRecord_Query { 
	            ${table} (queryConditions: "${queryConditions}", pagination: {limit: ${RELATED_ITEMS_PAGE_SIZE}, offset: 0}) 
	            {
                             ${allAttrStr}
                 }
            }
            
            GlideAggregateRecord_Query(tableName: "${table}",
		 				queryConditions: "${queryConditions}", groupBy: "${groupBy}") {
            totalCount
                aggregates {
                    displayValue: count
                }
            } 
            
    }`;
};

const createIndicatorsGraphQl = qlQuery => 
	createGraphQLEffect(qlQuery, {
		startActionType: effectGetIndicatorsForCis.ACTION_TYPES.MAP_GET_INDICATORS_FOR_CIS_REQUEST_STARTED,
		successActionType: effectGetIndicatorsForCis.ACTION_TYPES.MAP_GET_INDICATORS_FOR_CIS_REQUEST_SUCCEEDED,
		errorActionType: effectGetIndicatorsForCis.ACTION_TYPES.MAP_GET_INDICATORS_FOR_CIS_REQUEST_FAILED,
		variableList: ['queryConditions']
	});


