import {get as _get} from 'lodash';
import {LOGGING, snOpwsRelatedItemsListActionTypes} from '@devsnc/library-opws-mapping-shared';
import {TYPE_IDS} from '../map-shared/itemTypes';
import {effectMapGetAlertItems} from './effectMapGetAlertItems';
import effectGetMapChangeItems from './effectGetMapChangeItems';
import {getGraphQlMapIndicatorsEffect} from './getMapIndicatorsForCis';
import {setChangeCardsinHistory} from '../behavior-service-history/effectChangeCardInHistory';

const ACTION_TYPES = {
	REFRESH_REQUESTED: snOpwsRelatedItemsListActionTypes.REFRESH_REQUESTED
};

/**
 * Interceptor to navigate between multiple effects for specific fetch implementations.
 */
const effectMapRelatedItemsRefreshInterceptor = context => {
	const {coeffects} = context;
	const selectedType = _get(coeffects, 'action.payload.selectedType', null);
	const newEffects = [];
	let effect;
	if (!selectedType || !selectedType.id) {
		LOGGING.log('effectMapRelatedItemsRefreshInterceptor: doing nothing since no selectedType.id...');
		return context;
	}
	
	/*
	 * Implementation notes: 
	 * For each item type, have a call to the corresponsing effect in here, by adding the effect function into `newEffects` array.
	 * Make sure that you pass an object {effect: yourEffect, args: [coeffects]}, otherwise yourEffect will have no input.
	 * 
	 * yourEffect should provide a request that will in response
	 * Set property `relatedItemsResponse` 
	 * * items - array
	 * * lastUpdated timestamp
	 * * requestTime [optional] formated time for server.
	 * * totalRecords number 
	 */
	switch (selectedType.id) {
		case TYPE_IDS.ALERTS:
			newEffects.push({
				effect: effectMapGetAlertItems,
				args: [coeffects]
			});
			break;

		case TYPE_IDS.MAP_TOPO_CHANGES:
			effect = selectedType.isHistory ? 
				setChangeCardsinHistory : 
				effectGetMapChangeItems.effectHandlers.getMapChangeItems;

			newEffects.push({
				effect,
				args: [coeffects]
			});

			break;

		default:
			effect = getGraphQlMapIndicatorsEffect(coeffects);
			effect.args = [
				...effect.args,
				coeffects
			];

			newEffects.push(effect);
			break;
	}

	return {
		coeffects,
		effects: newEffects
	};
};


const effectMapRelatedItemsRefresh = {
	effect(coeffects) {
		/*
			This effect will run AFTER all other effects that were invoked from the interceptor.
			It takes care to synchronize the payload on `map-component` state.
			`relatedItemsRefreshParams` should not be addressed in any other effect that is invoked from the interceptor.
			Instead, use the payload data.
		*/
		const {
			updateState,
			action: {
				payload
			}
		} = coeffects;

		updateState({relatedItemsRefreshParams: payload});
	},

	interceptors: [
		effectMapRelatedItemsRefreshInterceptor
	]
};

const effectHandlers = {
	effectMapRelatedItemsRefresh
};

export default {
	ACTION_TYPES,
	effectHandlers
};