import timelineConstants from '../behavior-service-history/timelineConstants';
import 'sn-component-timeline';

export const getTimelineJsx = ({timelineData = {}}) => {
	const {
		ranges = [],
		events = [],
		totalRange,
		displayRange,
		selectedEvents,
		selectedRanges,
		highlightedEventIds
	} = timelineData;

	//some wierd invalid state: mapHistoryState is set, but timelineData is not entirely set
	//validating before rendering
	if (!totalRange || !displayRange) 
		return null;

	const {start: dispStart, end: dispEnd} = displayRange;
	const {start, end} = totalRange;

	return (
		<sn-component-timeline
			className='timeline top-side now-align -block-start'
			start-date={start}
			end-date={end}
			display-start-date={dispStart}
			display-end-date={dispEnd}
			event-data={events}
			range-data={ranges}
			zoom-step-percent={timelineConstants.ZOOM_STEP}
			min-zoom-spread='MN'
			pan-step-percent={timelineConstants.PAN_STEP}
			is-compact-view
			manage-selections
			selected-events={selectedEvents}
			selected-ranges={selectedRanges}
			highlighted-event-ids={highlightedEventIds}
		/>
	);
};
