
import MAP_CONSTANTS from '../map-shared/constants';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {formatComplexFieldsToCamelCase, getDisplayValue, LOGGING, checkForError, getValue , CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {
	camelCase as _camelCase,
	get as _get,
	isFunction as _isFunction,
	mapKeys as _mapKeys,
	mapValues as _mapValues
} from 'lodash';

const ACTION_TYPES = {
	MAP_LOAD_INITIAL_DATA: 'MAP_LOAD_INITIAL_DATA',
	MAP_LOAD_INITIAL_DATA_STARTED: 'MAP_LOAD_INITIAL_DATA_STARTED',
	MAP_LOAD_INITIAL_DATA_SUCCEEDED: 'MAP_LOAD_INITIAL_DATA_SUCCEEDED',
	MAP_LOAD_INITIAL_DATA_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const GQL_QUERY = `
query loadMapInitialData($serviceQuery: String! $sysPropertiesQuery: String!) {
	GlideDomain_Query {
	  user {
		sys_id
		dateTimeFormat
		timeZoneOffset
		preferences(name: ["noc_map.topo_changes_range"]) {
		  name
		  value
		}
	  }
	}
	GlideRecord_Query {
	  cmdb_ci_service_discovered(queryConditions: $serviceQuery) {
		_results {
		  name {
			displayValue
		  }
		  owned_by {
			value
			displayValue
			label
		  }
		  process_status {
			value
			displayValue
			label
		  }
		}
	  }
	  sys_properties(queryConditions: $sysPropertiesQuery){
		_results {
		  name {
			value
		  }
		  value{
			value
		  }
		}
	  }
	}
  }
`;

const mapRequestStaticDataEffect = createGraphQLEffect(GQL_QUERY, {
	startActionType: ACTION_TYPES.MAP_LOAD_INITIAL_DATA_STARTED,
	successActionType: ACTION_TYPES.MAP_LOAD_INITIAL_DATA_SUCCEEDED,
	errorActionType: ACTION_TYPES.MAP_LOAD_INITIAL_DATA_FAILED,
	variableList: ['serviceQuery', 'sysPropertiesQuery']
});

const SERVICE_RESPONSE_PATH = 'data.GlideRecord_Query.cmdb_ci_service_discovered._results[0]';
const PROPERTIES_RESPONSE_PATH = 'data.GlideRecord_Query.sys_properties._results';


const getVariables = serviceId => ({
	serviceQuery: `sys_id=${serviceId || '-1'}`,
	sysPropertiesQuery: concatinatingPropertiesNames()
});

const concatinatingPropertiesNames = () => {
	const names = Object.keys(MAP_CONSTANTS.SYS_PROPERTIES);
	const query = `name=${names.join('^ORname=')}`;
	return query;
};

const mapFieldValues = field => {
	const outField = formatComplexFieldsToCamelCase(field);

	//moving 'label' to 'displayName' to make reuse of filedGetters
	return {
		...outField,
		displayName: outField.label || '', //displayName returned by getDisplayName()
		label: null //label returned as fallback by getDisplayValue() and should be erased here
	};
};

const formatServiceResponse = payload => {
	let data = _get(payload, SERVICE_RESPONSE_PATH) || {};
	data = _mapKeys(data, (value, key) => _camelCase(key));
	data = _mapValues(data, mapFieldValues);

	return data;
};

const formatPropertiesRespones = payload => {
	const response = _get(payload, PROPERTIES_RESPONSE_PATH) || [];	
	//formating the resonse to array of object of name and value 
	const normalizedResponse = response.reduce((result, property)=>({
		...result,
		[getValue(property.name)]: getValue(property.value)
	}), {});

	const propertiesObj = _mapValues(MAP_CONSTANTS.SYS_PROPERTIES, (sanitizer, propertyName)=> sanitizer(normalizedResponse[propertyName]));

	return propertiesObj;
};


const setServiceNameOnMap = (setServiceNameCb, mapInitialData) => {
	if (_isFunction(setServiceNameCb))
		setServiceNameCb(getDisplayValue(mapInitialData.name));
};

const getUserMeta = payload => { 
	const record = _get(payload, 'data.GlideDomain_Query.user');

	const topologyChangesRangePref = _get(payload, 'data.GlideDomain_Query.user.preferences', [])
		.find(item => item.name === 'noc_map.topo_changes_range');

	const topologyChangesRange = Math.min(365, Math.max(0, (Number(topologyChangesRangePref.value) || 0)));

	return {
		sysId: record.sys_id,
		dateTimeFormat: record.dateTimeFormat,
		tz: record.timeZoneOffset,
		topologyChangesRange
	};
};

const mapStaticDataResponseEffect = coeffects => {
	LOGGING.log('mapStaticDataResponseEffect');
	const {
		state,
		updateState,
		updateProperties,
		dispatch,
		action: {payload}
	} = coeffects;
	if (checkForError({payload, dispatch, action: 'mapStaticDataResponseEffect'}))
		return;

	const mapInitialData = formatServiceResponse(payload);
	const updatedProperties = formatPropertiesRespones(payload);
	updateState({sysProperties: updatedProperties});
	const {setServiceNameCb} = state;
	setServiceNameOnMap(setServiceNameCb, mapInitialData);

	const userMeta = getUserMeta(payload);

	updateState({mapInitialData});
	updateProperties({userMeta});
};


const effectHandlers = {
	mapRequestStaticDataEffect,
	mapStaticDataResponseEffect
};

export default {
	ACTION_TYPES,
	effectHandlers,
	getVariables,
	setServiceNameOnMap
};