import {checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {
	camelCase as _camelCase, 
	get as _get, 
	reduce as _reduce
} from 'lodash';

import {t} from 'sn-translate';
import MAP_CONSTANTS from '../map-shared/constants';

const LABEL_LAST_UPDATED_TIME = t('Last Updated');
const TASK_CI_PREFIX = 'task_';
const CI_PREFIX = 'ci_';

const ACTION_TYPES = {
	MAP_GET_INDICATORS_FOR_CIS_REQUEST_STARTED: 'MAP_GET_INDICATORS_FOR_CIS_REQUEST_STARTED',
	MAP_GET_INDICATORS_FOR_CIS_REQUEST_SUCCEEDED: 'MAP_GET_INDICATORS_FOR_CIS_REQUEST_SUCCEEDED',
	MAP_GET_INDICATORS_FOR_CIS_REQUEST_FAILED: 'MAP_GET_INDICATORS_FOR_CIS_REQUEST_FAILED'
};

/**
 * Standartize content complex field to always have expected displayName, value, displayValue.
 * @param {Object} field the object containing displayName, value, label, etc...
 * @param {String} fieldKey - key of the field (DB view prefixes are removed)
 */
const formatFieldValue = (field, fieldKey) => 
	(field && {
		displayName: fieldKey === 'sys_updated_on' ? LABEL_LAST_UPDATED_TIME : field.label,
		value: field.value,
		displayValue: field.displayValue
	});

const removePrefix = key => {
	if (key.startsWith(TASK_CI_PREFIX))
		return key.slice(TASK_CI_PREFIX.length);
	else if (key.startsWith(CI_PREFIX))
		return key.slice(CI_PREFIX.length);
	return key;
};

/**
 * Operator function for reduce that modifies keys and value of item returned in grapQl response.
 * @param {Object} item - accumulator for the reduce result.
 * @param {Object} value - for complex field returned from graphQL, this is the object containing displayName, value, label, etc...
 * @param {String} key - the key of the field
 */
const reduceItemFields = (ciFieldName, item, value, key) => {
	//getting rid of DB view prefixes of fields of affected CIs query.
	const keyNoPrefix = removePrefix(key);

	const newKey = keyNoPrefix === ciFieldName ? 
		'ciField' :  //standartize `ciField`, copy it from dynamic [ciFieldName]
		_camelCase(keyNoPrefix); //format any other keys to camel case

	const newValue = formatFieldValue(value, keyNoPrefix);
	return {
		...item,
		[newKey]: newValue
	};
};

const formatItem = ({ciFieldName}, rawItem) => {
	const item = _reduce(rawItem, reduceItemFields.bind(null, ciFieldName), {});
	
	return {
		...item,
		id: item.sysId.value,
		number: (item.number || item.task)
	};
};

const handleGetMapIndicatorsForCisResponse = coeffects => {
	LOGGING.info('handleGetMapIndicatorsForCisResponse', {caller: 'handleGetMapIndicatorsForCisResponse'});

	const {
		properties: {
			relatedItemIds = {}
		},
		updateProperties,
		dispatch,
		action: {payload},
		state
	} = coeffects;

	const totalRecords = _get(payload, 'data.GlideAggregateRecord_Query.totalCount', 0);

	const errors = checkForError({payload, dispatch, caller: 'handleGetMapIndicatorsForCisResponse'});
	if (errors)
		return;

	const selectedindicatorId = _get(state, 'relatedItemsRefreshParams.selectedType.id') || '';
	const ciFieldName = _get(state, `indicatorsPanelData['${selectedindicatorId}'].urlData.params.sysparm_cmdb_ci_field`) || '';

	//for history changes, the size of this array shows whether map element was selected
	const selectedItemIds = _get(relatedItemIds, `['${selectedindicatorId}'].sysIds`) || [];
	
	//`isMapElementSelected` populated only for indicators. Otherwise falling back on selectedItemIds size
	const isMapElementSelected = _get(relatedItemIds, `['${selectedindicatorId}'].isMapElementSelected`, (selectedItemIds.length !== 0));

	const queryRes = Object.values(payload.data.GlideRecord_Query);
	const results = _get(queryRes, '[0]._results') || [];
	const items = results.map(formatItem.bind(null, {ciFieldName}));

	updateProperties({
		relatedItemsResponse: {
			items,
			totalRecords,
			lastUpdated: Date.now(),
			filterApplied: {
				type: isMapElementSelected ? MAP_CONSTANTS.CONTEXT_SELECTION_STATES.MAP : null
			}
		},
		filteredItems: results
	});
};


const effectHandlers = {
	handleGetMapIndicatorsForCisResponse
};

export default {
	ACTION_TYPES,
	effectHandlers
};