import {createCardComponent, generateViewFn} from '@devsnc/library-opws-mapping-shared';
import {getChangeCardContentJsx as getCardContentJsx} from './getChangeCardContentJsx';
import {ACTION_TYPES} from './actionTypes';

const view = generateViewFn({
	getCardContentJsx,
	openFormActionType: false,
	cardSelectedActionType: ACTION_TYPES.TOPO_CHANGE_CARD_SELECTED,
	size: 'lg'
});

/**
 * Seismic component `sn-map-change-card`.
 * Card for map topology changes to show on side panel on nocpit and on map.
 **/
createCardComponent({
	componentName: 'sn-map-change-card',
	view
});