import {t} from 'sn-translate';
import '@servicenow/now-label-value';
import {
	CONSTANTS,
	getValue,
	getDisplayValue,
	getDisplayName
} from '@devsnc/library-opws-mapping-shared';

const LABEL_SEVERITY = t('Severity');

const getSeverityHighlightedItem = ({isEm, serviceSeverity}) => {
	if (!isEm) return [];

	const severityValue = getValue(serviceSeverity);
	const severityLabel = getDisplayValue(serviceSeverity);
	const severityHighlightedItem = severityLabel
		? {
			type: 'highlighted-value',
			showIcon: true,
			label: severityLabel,
			status: CONSTANTS.GENERAL.EM_SEVERITY_TO_EDS_STATUS[severityValue]
		}
		: {
			type: 'string',
			value: ''
		};

	return [
		{
			label: LABEL_SEVERITY,
			value: severityHighlightedItem
		}
	];
};

export const getHeaderFieldsJsx = args => {
	const {mapInitialData} = args;

	if (!mapInitialData) return null;

	const {processStatus, ownedBy} = mapInitialData;

	const mapTitleItems = [
		...getSeverityHighlightedItem(args),
		{
			label: getDisplayName(processStatus),
			value: {
				type: 'string',
				value: getDisplayValue(processStatus)
			}
		},
		{
			label: getDisplayName(ownedBy),
			value: {
				type: 'string',
				value: getDisplayValue(ownedBy)
			}
		}
	];

	return (
		<now-label-value-stacked
			className='top-side'
			size='md'
			align='horizontal'
			items={mapTitleItems}
			has-no-margin
		/>
	);
};
