import MAP_CONSTANTS from '../map-shared/constants';

const ACTION_TYPES = {
	MAP_IS_LOADED: 'MAP_INITIAL_DATA_BEHAVIOR#MAP_IS_LOADED'
};

const mapIsLoaded = coeffects => {
	const {
		state: {loadingState, isInitializing, isBlocked},
		updateState
	} = coeffects;
	
	if (isInitializing)
		updateState({isInitializing: false});

	if (isBlocked)
		updateState({loadingState: MAP_CONSTANTS.LOADING_STATES.BLOCKED});

	else if (loadingState !== MAP_CONSTANTS.LOADING_STATES.DONE)
		updateState({loadingState: MAP_CONSTANTS.LOADING_STATES.DONE});
};

const effectHandlers = {
	mapIsLoaded
};

export default {
	ACTION_TYPES,
	effectHandlers
};