import {t} from 'sn-translate';

/**
 * This structure defines labels and ordering of sections in settings modal.
 * It also defines the controls that will be displayed in each section, and the order of apeparance.
 */
export const settingsMenuSections = [
	{
		label: t('Map Display'),
		keys: ['hostView', 'autoGroupView', 'removeLoops', 'trafficBaseDiscovery','blockLargeService'],
		id: 'map_display'
	},
	{
		label: t('Map Layout'),
		keys: ['shortestPathView'],
		id: 'map_layout'
	},
	{
		label: t('Map Indicators'),
		keys: [],
		id: 'map_indicators'
	}

];

export const getIndicatorsSection = () =>
	settingsMenuSections.find(section => section.id === 'map_indicators');