/**
 * Hard-coded list of static item types that are displayed on map.
 * This is a base list, it doesn't contain indicators, that are added dynamically from map event.
 * Each type should include 
 * - id: String
 * - label: translated String
 * - cardComponentName: remember to import the component so it will be registered on DOM.
 * - sortOptions: hardcoded list of all sorting options for the type: [{id, label, order, field}]
 */
