import {get as _get} from 'lodash';

const DROPDOWN_ID = 'headerMoreItems';

const ITEM_IDS = {
	FULL_MAP: 'FULL_MAP'
};

const ACTION_TYPES = {
	ITEM_CLICKED: 'NOW_DROPDOWN#ITEM_CLICKED'
};

const openFullMap = (serviceId, isEm, serviceName) => {
	const baseURL = `/$sw_topology_map.do?sysparm_bsid=${serviceId}&sysparm_bsname=${serviceName}`;
	
	if (isEm) 
		window.open(`${baseURL}&sysparam_back_ref=service_auto_table&sysparm_plugin_mode=assurance`, serviceId);
	else 
		window.open(`${baseURL}&sysparm_plugin_mode=mapping`, serviceId);
};

/**
 * This effect intercepts only the "more items" dropdown from the <map-header-panel>.
 * 
 * @param {*} coeffects 
 */
const onDropdownItemClicked = coeffects => {
	const {
		action: {
			payload: {
				dropdownId,
				item
			}
		},
		properties: {
			serviceId, 
			isEm,
			mapInitialData
		}
	} = coeffects;

	if (DROPDOWN_ID !== dropdownId || !item)
		return;

	if (item.id === ITEM_IDS.FULL_MAP) {
		const serviceName = _get(mapInitialData, 'name.displayValue');
		openFullMap(serviceId, isEm, serviceName);
	}
};

const effectHandlers = {
	onDropdownItemClicked
};

export default {
	ACTION_TYPES,
	effectHandlers,
	DROPDOWN_ID,
	ITEM_IDS
};