import {snOpwsRelatedItemsListActionTypes} from '@devsnc/library-opws-mapping-shared';
import mapEventsWhitelist from './mapEventsWhitelist';
import constants from '../map-shared/constants';
import {
	get as _get, 
	isFunction as _isFunction,
	memoize as _memoize
} from 'lodash';

import mapSettingsModifiedEffect from '../map-settings-menu-behavior/mapSettingsModifiedEffect';
import mapServiceSeverityEffect from '../map-initial-data-behavior/mapServiceSeverityEffect';
import mapStaticDataEffect from '../map-initial-data-behavior/mapStaticDataEffect';
import effectMapIsLoaded from '../map-initial-data-behavior/effectMapIsLoaded';
import {TYPE_IDS} from '../map-shared/itemTypes';
import {setHistoryChangesFilter} from '../map-shared/setHistoryChangesFilter';
import {syncBlockingState, showBlockedMap} from './mapBlocking';

const ACTION_TYPES = {
	NOC_EVENT_FROM_MAP_ARRIVED: constants.NOC_EVENT_FROM_MAP_ARRIVED
};

/**
 * The cost of redundant state updates is too high.
 * This function helps to prevent state updates and filter out repeated events from map.
 * @param {*} indicatorId 
 * @param {*} currIndicatorsPanelData 
 * @param {*} newUpdate 
 */
const shouldUpdateIndicatorsPanelData = (indicatorId, currIndicatorsPanelData, newUpdate) => {
	if (!currIndicatorsPanelData[indicatorId])
		return true;

	const currentTimestamp = currIndicatorsPanelData[indicatorId].arrivedAt;
	const newTimestamp = newUpdate.arrivedAt;

	if (currentTimestamp === newTimestamp)
		return false;

	const currHash = currIndicatorsPanelData[indicatorId].sysIds.join(',');
	const newHash = newUpdate.sysIds.join(',');

	return currHash.length !== newHash.length || currHash !== newHash;
};

const getIndicatorId = args =>
	_get(args, '[0].urlData.params.sysparm_tab_id') || '';


/**
 * Processing events from map that can cause refresh of the side panel.
 * `currItemIds` - payload from the map, contaiing details on update in contextual panel.
 * `relatedItemsRefreshParams` - cached params of previos refresh
 */
const refreshRelatedItems = ({currItemIds, relatedItemsRefreshParams, dispatch}) => {
	const {isInteractive = false} = currItemIds;

	//stop auto-refresh for pulling manager updates. 
	//Only map request that is marked as interactive, will cause the refresh.
	if (!isInteractive)
		return;

	dispatch(() => ({
		type: snOpwsRelatedItemsListActionTypes.REFRESH_REQUESTED, 
		payload: {
			...relatedItemsRefreshParams,
			forceRefresh: true
		}
	}));
};

const updateRelatedItems = ({
	typeId, 
	currItemIds, 
	mapPayload, 
	relatedItemsRefreshParams, 
	dispatch, 
	updateProperties,
	arrivedAt = Date.now()
}) => {
	
	setHistoryChangesFilter({
		dispatch, 
		updateProperties, 
		itemType: typeId, 
		currItemIds, 
		relatedItemIds: {
			...mapPayload,
			arrivedAt
		}
	});

	//don't refresh for non-selected type
	if (typeId !== _get(relatedItemsRefreshParams, 'selectedType.id'))
		return;

	refreshRelatedItems({currItemIds: mapPayload, relatedItemsRefreshParams, dispatch});
};

const onGetServiceName = ({args, mapInitialData, updateState}) => {
	const setServiceNameCb = args[0];
	if (_isFunction(setServiceNameCb)) {
		updateState({setServiceNameCb});

		if (mapInitialData)
			mapStaticDataEffect.setServiceNameOnMap(setServiceNameCb, mapInitialData);
	}
};

const onIndicatorPanelUpdate = ({
	state, 
	args, 
	currItemIds, 
	dispatch, 
	updateProperties,
	updateState
}) => {

	const {
		relatedItemsRefreshParams,
		indicatorsPanelData
	} = state;

	const arrivedAt = Date.now();
	const indicatorId = getIndicatorId(args);
	const updatedIndicatorData = {
		...args[0],
		arrivedAt
	};

	if (shouldUpdateIndicatorsPanelData(indicatorId, indicatorsPanelData || {}, updatedIndicatorData)) {

		updateState({
			indicatorsPanelData: {
				...indicatorsPanelData,
				[indicatorId]: updatedIndicatorData
			}
		});

		updateRelatedItems({
			typeId: indicatorId, 
			currItemIds, 
			mapPayload: args[0], 
			relatedItemsRefreshParams, 
			dispatch, 
			updateProperties,
			arrivedAt
		});
	}
};

const onSeverityColorsGetter = ({args, updateState}) => {
	const severitiesGetter = args[0];
	
	if (_isFunction(severitiesGetter)) {
		const optimizedGetter = _memoize(severity => {
			const color = severitiesGetter(severity);
			return (color === 'transparent') ? null : color;
		});

		updateState({
			severitiesGetter: optimizedGetter
		});
	}
};

const resolveLoadingState = ({updateState, payload = {}}) => {
	const {
		isLoaded = false,
		isFailed = false,
		isBlocked = false
	} = payload;

	const loadingState = isBlocked ?
		constants.LOADING_STATES.BLOCKED :
		isFailed ? 
			constants.LOADING_STATES.ERROR : 
			isLoaded ? 
				constants.LOADING_STATES.DONE : 
				constants.LOADING_STATES.LOADING;

	updateState({loadingState});
};

/**
 * Reducer-like effect that should catch all white-listed map events and decide what to do with it.
 */
const mapEventReceivedEffect = coeffects => {
	const {
		state,
		properties: {
			relatedItemIds: currItemIds
		},
		action,
		dispatch,
		updateProperties,
		updateState
	} = coeffects;

	const {
		relatedItemsRefreshParams,
		mapInitialData,
		isInitialized
	} = state;

	const {name, args} = action.payload;

	switch(name) {
		case mapEventsWhitelist.EVT_WS_GET_SERVICE_NAME:
			onGetServiceName({args, mapInitialData, updateState});
			break;

		case mapEventsWhitelist.EVT_WS_SEVERITY_COLORS_GETTER_SENT:
			onSeverityColorsGetter({args, updateState});
			break;

		case mapEventsWhitelist['sa.refreshPropertiesPane']:
			updateProperties({
				mapProperties: {
					...args[0],
					arrivedAt: Date.now()
				}
			});
			break;

		case mapEventsWhitelist.EVT_WS_ALERT_PANEL_UPDATE:
			updateRelatedItems({
				typeId: TYPE_IDS.ALERTS, 
				currItemIds, 
				mapPayload: args[0], 
				relatedItemsRefreshParams, 
				dispatch, 
				updateProperties

			});
			break;

		case mapEventsWhitelist.EVT_WS_HISTORY_CHANGES_PANEL_UPDATE:
			updateRelatedItems({
				typeId: TYPE_IDS.MAP_TOPO_CHANGES, 
				currItemIds, 
				mapPayload: args[0], 
				relatedItemsRefreshParams, 
				dispatch, 
				updateProperties
			});
			break;

		case mapEventsWhitelist.EVT_WS_INDICATOR_PANEL_UPDATE:
			onIndicatorPanelUpdate({state, args, currItemIds, dispatch, updateProperties, updateState});
			break;

		case mapEventsWhitelist.sa_status_change:
			dispatch(mapServiceSeverityEffect.ACTION_TYPES.MAP_TIMESTAMP_CHANGED, {timestamp: args[0]});
			break;

		case mapEventsWhitelist['snc.layoutReapplied']:
			dispatch(effectMapIsLoaded.ACTION_TYPES.MAP_IS_LOADED);
			break;


		case mapEventsWhitelist.wsMapLoaderStateChanged:
			resolveLoadingState({updateState, payload: args[0]});
			break;

		case mapEventsWhitelist['snc.mapBlockingStateChanged']: 
			//simulate map is loaded state
			if (!isInitialized)
				dispatch(effectMapIsLoaded.ACTION_TYPES.MAP_IS_LOADED);

			syncBlockingState({graphDTOData: args[0], updateState});
			break;

		case mapEventsWhitelist['sa.blockLargeServiceChanged']:
			dispatch(mapSettingsModifiedEffect.ACTION_TYPES.NOC_SETTINGS_MODIFIED_EVENT, action.payload);
			showBlockedMap({dispatch, updateState, rememberForSession: false})();
			break;

		default:
			//events that not catched here are redirected to other effects accoridng to white lists
			if (mapSettingsModifiedEffect.MAP_EVENT_NAMES[name]) 
				dispatch(mapSettingsModifiedEffect.ACTION_TYPES.NOC_SETTINGS_MODIFIED_EVENT, action.payload);
			
			break;
	}
};

const effectHandlers = {
	mapEventReceivedEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};
