import {get as _get, debounce as _debounce} from 'lodash';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import MAP_CONSTANTS from '../map-shared/constants';
import {sanitizeHistoryRange, formatRange, isRangeIncluded} from './timelineFunctors';
import effectReceiveHistoryData from './effectReceiveHistoryData';
import {clearMapSelection, clearCardSelection} from '../map-shared/setHistoryChangesFilter';

const ACTION_TYPES = {
	TIMELINE_RANGE_UPDATED: 'TIMELINE_CHART#RANGE_CHANGE'
};

const _applyCandidateRange = ({updateProperties, dispatch, candidateRange, serviceId, currDataRange}) => {
	const timelineCandidates = {
		displayRange: {
			start: candidateRange.start,
			end: candidateRange.end
		}
	};

	LOGGING.debug(`TIMELINE_CHART#RANGE_CHANGE: range changing (debounced)
		${formatRange(candidateRange.start, candidateRange.end)}`
	);

	updateProperties({timelineCandidates});

	//clearing current selection states on timeline range change.
	//the timeline seelction state is cleared from effect resolveHistoryState [on MAP_HISTORY_DATA_SET].
	clearMapSelection(dispatch);
	clearCardSelection(dispatch);
	
	if (isRangeIncluded(candidateRange, currDataRange)) {
		//candidate range is in current dataset.
		dispatch(() => ({
			type: effectReceiveHistoryData.ACTION_TYPES.MAP_HISTORY_DATA_SET
		}));

	} else {
		//candidate range is not in current dataset.
		//trigger request for history data
		dispatch(effectReceiveHistoryData.ACTION_TYPES.MAP_HISTORY_DATA_REQUEST, {
			serviceId,
			showSeverity: false, //For Paris release, there's no severity on the timeline
			startTime: candidateRange.start,
			endTime: candidateRange.end
		});
	}
};

const RANGE_CHANGE_DEBOUNCE_PERIOD = 1000;
const applyCandidateRange = _debounce(_applyCandidateRange, RANGE_CHANGE_DEBOUNCE_PERIOD);

const onTimelineRangeUpdated = coeffects => {
	const {
		action: {
			payload: {
				currentViewStart: start,
				currentViewEnd: end
			}
		},
		updateProperties,
		updateState,
		dispatch,
		properties: {
			timelineData = {},
			serviceCreationTime,
			serviceId,
			currDataRange,
			upperDateLimit
		}
	} = coeffects;

	const currStart = _get(timelineData, 'displayRange.start') || -1;
	const currEnd = _get(timelineData, 'displayRange.end') || -1;

	const currentDisplayRange = {
		start: currStart,
		end: currEnd
	};

	if (start === currStart && end === currEnd) {
		LOGGING.debug('TIMELINE_CHART#RANGE_CHANGE: range not modified. Skipping.');
		return;
	}

	const candidateRange = sanitizeHistoryRange({serviceCreationTime, upperDateLimit, start, end});

	if (candidateRange.start === currStart && candidateRange.end === currEnd) {
		LOGGING.debug('TIMELINE_CHART#RANGE_CHANGE: sanitized range not modified. Skipping.');
		return;
	}

	//enable map loader on timeline interaction
	updateState({
		loadingState: MAP_CONSTANTS.LOADING_STATES.LOADING
	});

	applyCandidateRange({updateProperties, dispatch, candidateRange, currDataRange, currentDisplayRange, serviceId});
};

const effectHandlers = {
	onTimelineRangeUpdated
};

export default {
	ACTION_TYPES,
	effectHandlers
};