import {t} from 'sn-translate';
import MAP_CONSTANTS from '../map-shared/constants';
import {TYPE_IDS} from '../map-shared/itemTypes';
import {DEFAULT_RANGE_DAYS} from '../map-container/effectGetMapChangeItems';

/**
 * Returns builder function for sn-opws-related-items-list.alt-footer-text-builder;
 * Overrides footer text for changes panel in current map, when the list is not empty.
 */
export const getAltFooterBuilder = ({items, mapHistoryState, topologyChangesRange}) => selectedType => {
	const count = items.length || 0;
	const nDays = Number(topologyChangesRange) || DEFAULT_RANGE_DAYS;

	return (count > 0 && 
    mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT && 
    selectedType.id === TYPE_IDS.MAP_TOPO_CHANGES) ?
		t(`${count} change(s) in last ${nDays} days. See history map for more.`) : 
		null; 
};