import {INDICATOR_IDS} from '../map-shared/indicatorTypes';
import {
	getIndicatorCardWithAvatarJsx, 
	getAvatarElementJsx, 
	getLabelValueElementJsx
} from '@devsnc/library-opws-mapping-shared';

export const getChangeRequestCardContentJsx = (item, selectedTab) => {
	const {
		assignedTo,
		assignmentGroup,
		startDate, // planned
		endDate,
		workStart, // actual
		workEnd
	} = item;

	let startTime = '';
	let endTime = '';

	switch (selectedTab) {
		case INDICATOR_IDS.MAP_CHANGE_REQUEST_CURRENT:
			startTime = workStart;
			break;
		case INDICATOR_IDS.MAP_CHANGE_REQUEST_PLANNED:
			startTime = startDate;
			endTime = endDate;
			break;
		case INDICATOR_IDS.MAP_CHANGE_REQUEST_PAST:
			startTime = workStart;
			endTime = workEnd;
			break;

	}

	const items = {
		1: {jsxBuilder: getLabelValueElementJsx, field: startTime},
		2: {jsxBuilder: getLabelValueElementJsx, field: endTime},
		3: {jsxBuilder: getAvatarElementJsx, field: assignedTo},
		4: {jsxBuilder: getLabelValueElementJsx, field: assignmentGroup},
	};

	const cardJsx = getIndicatorCardWithAvatarJsx(items);
	return (
		<div>
			{cardJsx}
		</div>
	);
};