import {forceTimelineRefresh} from '../behavior-service-history/timelineFunctors';

const ACTION_TYPES = {
	TOGGLE_SIDE_PANEL: 'NOW_RECORD_COMMON_SIDEBAR#OPENED_SET'
};

const toggleSidePanel = coeffects => {
	const {
		updateProperties
	} = coeffects;

	//DEF0109873: had to delay this updateProperties to make sure the timeline refreshes AFTER the side panel layout was re-rendered.
	setTimeout(() => forceTimelineRefresh(updateProperties), 100);
};

const effectHandlers = {
	toggleSidePanel
};

export default {
	ACTION_TYPES,
	effectHandlers
};