
import {createHttpEffect} from '@servicenow/ui-effect-http';
import sendEventToMapEffect from '../map-container/sendEventToMapEffect';
import {checkForError, LOGGING, CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {get as _get} from 'lodash';

const ACTION_TYPES = {
	MAP_GET_CIS_OF_ALERT_REQUEST: 'MAP_GET_CIS_OF_ALERT_REQUEST',
	MAP_GET_CIS_OF_ALERT_STARTED: 'MAP_GET_CIS_OF_ALERT_STARTED',
	MAP_GET_CIS_OF_ALERT_SUCCEEDED: 'MAP_GET_CIS_OF_ALERT_SUCCEEDED',
	MAP_GET_CIS_OF_ALERT_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED,
};

const getAffectedCis = coeffects => {
	const {
		action: {payload},
		updateState,
		dispatch
	} = coeffects;

	const {
		item,
		isSelected
	} = payload;

	if (!isSelected) {
		//reset mark on map
		markCisOnMap(dispatch, []);
		return;
	}

	const{
		alertHistorySysId: alertHistoryId
	} = item;

	updateState({item});

	dispatch(ACTION_TYPES.MAP_GET_CIS_OF_ALERT_REQUEST, {
		data: {alertHistoryId}
	});

};

const getAffectedCisRequest = createHttpEffect('/api/sn_nocpit/nocpit/getAllCisOfAlertByMode', {
	method: 'POST',
	startActionType: ACTION_TYPES.MAP_GET_CIS_OF_ALERT_STARTED,
	successActionType:ACTION_TYPES.MAP_GET_CIS_OF_ALERT_SUCCEEDED,
	errorActionType: ACTION_TYPES.MAP_GET_CIS_OF_ALERT_FAILED
});

export const markCisOnMap = (dispatch, ciIds) =>
	dispatch(sendEventToMapEffect.ACTION_TYPES.NOC_EVENT_TO_MAP_SENT, {
		args: [ciIds],
		name: sendEventToMapEffect.ACTION_TYPES['sa.markNodes']
	});

const handleGetCisResponse = coeffects => {
	LOGGING.info('handleGetCisResponse', {caller: 'handleGetCisResponse'});

	const {
		dispatch,
		action: {payload},
		state,
	} = coeffects;


	const errors = checkForError({payload, dispatch, caller: 'handleGetCisResponse'});
	if (errors)
		return;

	const ciId = _get(state, 'item.ciId');
    
	const ciIds = [
		ciId,
		...payload
	];
    
	markCisOnMap(dispatch, ciIds);
};

const markChangeOnMap = coeffects => {
	const {dispatch} = coeffects;
	const isSelected = _get(coeffects, 'action.payload.isSelected', false);

	if (!isSelected) {
		//reset mark on map
		markCisOnMap(dispatch, []);
		return;
	}

	const ciId = _get(coeffects, 'action.payload.item.ciId', '');
	//if ciId is not defined for some unexpected reason, the mark will be reset on map (expected result)
	markCisOnMap(dispatch, [ciId]);
};

const effectHandlers = {
	getAffectedCis,
	getAffectedCisRequest,
	handleGetCisResponse,
	markChangeOnMap
};

export default {
	effectHandlers,
	ACTION_TYPES
};