import {createHttpEffect} from '@servicenow/ui-effect-http';
import {CONSTANTS, checkForError, formatComplexFieldsToCamelCase} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
	MAP_TIMESTAMP_CHANGED: 'MAP_TIMESTAMP_CHANGED',
	MAP_GET_SERVICE_SEVERITY: 'MAP_GET_SERVICE_SEVERITY',
	MAP_GET_SERVICE_SEVERITY_STARTED: 'MAP_GET_SERVICE_SEVERITY_STARTED',
	MAP_GET_SERVICE_SEVERITY_SUCCEEDED: 'MAP_GET_SERVICE_SEVERITY_SUCCEEDED',
	MAP_GET_SERVICE_SEVERITY_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const mapServiceSeverityTimestampChangedEffect = coeffects => {
	const {
		properties,
		action: {
			payload
		}, 
		dispatch
	} = coeffects;
	
	if (checkForError({payload, dispatch, caller: 'mapServiceSeverityTimestampChangedEffect'}))
		return;
		
	const {timestamp} = payload;
	const {serviceId, isEm} = properties;

	if (!isEm)
		return;

	if (timestamp)
		dispatch(ACTION_TYPES.MAP_GET_SERVICE_SEVERITY, {serviceId, timestamp});
};

//isEM is valid to be true ONLY is nocpit plugin is installed (together with EM). Then this API will exist.
const getServiceSeverityEffect = createHttpEffect('/api/sn_nocpit/nocpit/ServiceSeverity', {
	method: 'GET',
	queryParams: ['serviceId', 'timestamp'],
	startActionType: ACTION_TYPES.MAP_GET_SERVICE_SEVERITY_STARTED,
	successActionType: ACTION_TYPES.MAP_GET_SERVICE_SEVERITY_SUCCEEDED,
	errorActionType: ACTION_TYPES.MAP_GET_SERVICE_SEVERITY_FAILED,
});

const serviceSeverityResponseEffect = coeffects => {
	const {
		state,
		action: {
			payload
		},
		updateState
	} = coeffects;

	const severity = formatComplexFieldsToCamelCase(payload);

	if (!severity || state.serviceSeverity === severity)
		return;

	updateState({serviceSeverity: severity});
};

const effectHandlers = {
	mapServiceSeverityTimestampChangedEffect,
	getServiceSeverityEffect,
	serviceSeverityResponseEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};