import {dispatchEventToMap} from '../map-container/eventBus';
import timelineConstants from '../behavior-service-history/timelineConstants';
import {isHistoryMap, forceTimelineRefresh} from '../behavior-service-history/timelineFunctors';
import sendEventToMapEffect from './sendEventToMapEffect';

const ACTION_TYPES = {
	MAP_VISIBILITY_CHANGE:'MAP_VISIBILITY_CHANGE'
};

/**
 * This effect triggers on-connect effect,
 * each time when visibility of the component on DOM changes from hidden to visible.
 */
const onMapVisibilityChange = coeffects => {
	const {
		properties: {
			mapId,
			mapHistoryState
		},
		action: {
			payload: {isVisible}
		},
		updateProperties
	} = coeffects;

	if (isVisible) {
		dispatchEventToMap(
			timelineConstants.EVT_ENABLE_PULLING_MANAGER,
			[],
			mapId
		);
		dispatchEventToMap(
			timelineConstants.EVT_RESUME_MASHUPS,
			[],
			mapId
		);

		//force the timeline re-render to make sure it re-sizes itself
		if (isHistoryMap(mapHistoryState))
			forceTimelineRefresh(updateProperties);

	
		//trigger map re-rendering and fit-to-screen
		dispatchEventToMap(sendEventToMapEffect.ACTION_TYPES.EVT_SCHEDULE_FIT_TO_SCREEN, [], mapId);
		dispatchEventToMap(sendEventToMapEffect.ACTION_TYPES.EVT_LAYOUT_REAPPLIED, [], mapId);
		
	} else { //map is hidden
		dispatchEventToMap(
			timelineConstants.EVT_DISABLE_PULLING_MANAGER,
			[],
			mapId
		);
		dispatchEventToMap(
			timelineConstants.EVT_STOP_MASHUPS,
			[],
			mapId
		);
	}
		
};

const effectHandlers = {
	onMapVisibilityChange
};

export default {
	ACTION_TYPES,
	effectHandlers
};