/**
 * effectInitHistoryMode - on load of history map (is-history=true),
 * this effect initializes the history state and triggers first historyData request.
 */
import timelineConstants from './timelineConstants';
import {dispatchEventToMap} from '../map-container/eventBus';
import effectReceiveHistoryData from './effectReceiveHistoryData';

const ACTION_TYPES = {
	MAP_INIT_HISTORY: 'MAP_INIT_HISTORY'
};

const effectInitHistoryMode = coeffects => {
	const {
		properties: {
			isHistory,
			initialRange,
			serviceId,
			mapId
		},
		dispatch,
		updateProperties
	} = coeffects;
	
	if (!isHistory)
		return;

	const candidateRange = (initialRange || {
		//ensuring default initial range
		start: Date.now() - timelineConstants.DEFAULT_RANGE_PERIOD,
		end: Date.now()
	});

	const {start: startTime, end: endTime} = candidateRange;

	//send event to angular to stop pulling manager
	dispatchEventToMap(
		timelineConstants.EVT_DISABLE_PULLING_MANAGER,
		{},
		mapId
	);

	updateProperties({
		initialRange: candidateRange,
		timelineCandidates: {
			displayRange: candidateRange
		}
	});

	dispatch(() => ({
		type: effectReceiveHistoryData.ACTION_TYPES.MAP_HISTORY_DATA_REQUEST,
		payload: {
			serviceId,
			showSeverity: false, //For Paris release, there's no severity on the timeline
			startTime,
			endTime
		}
	}));
};

const effectHandlers = {
	effectInitHistoryMode
};

export default {
	ACTION_TYPES,
	effectHandlers
};
