import cuid from 'cuid';
import {createActionConfig} from '@devsnc/library-opws-mapping-shared';
import MAP_CONSTANTS from '../map-shared/constants';
import mapStaticDataEffect from './mapStaticDataEffect';
import mapServiceSeverityEffect from './mapServiceSeverityEffect';
import effectMapIsLoaded from './effectMapIsLoaded';

const initFlowHandler = coeffects => {
	const {
		properties: {
			serviceId
		}, 
		dispatch,
		updateProperties
	} = coeffects;

	//set map id for all to use.
	const mapId = cuid();
	updateProperties({mapId});

	const initialDataVars = mapStaticDataEffect.getVariables(serviceId);
	dispatch(mapStaticDataEffect.ACTION_TYPES.MAP_LOAD_INITIAL_DATA, initialDataVars);
};

export const mapInitialDataBehavior = {
	name: 'mapInitialDataBehavior',
	actionHandlers: {
		[MAP_CONSTANTS.SN_MAP_CONNECTED]:
			createActionConfig(initFlowHandler),

		[effectMapIsLoaded.ACTION_TYPES.MAP_IS_LOADED]:
			createActionConfig(effectMapIsLoaded.effectHandlers.mapIsLoaded),

		[mapStaticDataEffect.ACTION_TYPES.MAP_LOAD_INITIAL_DATA]:
			createActionConfig(mapStaticDataEffect.effectHandlers.mapRequestStaticDataEffect),

		[mapStaticDataEffect.ACTION_TYPES.MAP_LOAD_INITIAL_DATA_SUCCEEDED]:
			createActionConfig(mapStaticDataEffect.effectHandlers.mapStaticDataResponseEffect),

		[mapServiceSeverityEffect.ACTION_TYPES.MAP_TIMESTAMP_CHANGED]:
			createActionConfig(mapServiceSeverityEffect.effectHandlers.mapServiceSeverityTimestampChangedEffect),

		[mapServiceSeverityEffect.ACTION_TYPES.MAP_GET_SERVICE_SEVERITY]:
			createActionConfig(mapServiceSeverityEffect.effectHandlers.getServiceSeverityEffect),

		[mapServiceSeverityEffect.ACTION_TYPES.MAP_GET_SERVICE_SEVERITY_SUCCEEDED]:
			createActionConfig(mapServiceSeverityEffect.effectHandlers.serviceSeverityResponseEffect)
	}
};

export const ACTION_TYPES = { 
	...mapStaticDataEffect.ACTION_TYPES
};