import {get as _get} from 'lodash';
import {CONSTANTS, checkForError, LOGGING, formatAlertsResponse} from '@devsnc/library-opws-mapping-shared';
import MAP_CONSTANTS from '../map-shared/constants';

const handleGetMapAlertsResponse = coeffects => {
	LOGGING.info('handleGetMapAlertsResponse', {caller: 'handleGetMapAlertsResponse'});

	const {
		updateProperties,
		dispatch,
		action: {payload},
		properties: {
			serviceId,
			relatedItemIds = {}
		}
	} = coeffects;

	const {
		alerts,
		totalRecords,
		requestedTime,
	} = payload;

	const errors = checkForError({payload, dispatch, caller: 'handleGetMapAlertsResponse'});
	if (errors) {
		updateProperties({cardsAreLoading:false});
		return;
	}

	const alertsSafe = alerts || [];
	const items = formatAlertsResponse(alertsSafe, serviceId);
	const alertsSelectionState = _get(relatedItemIds, 'ALERTS.urlData.extArgs.listof') || '';

	updateProperties({
		cardsAreLoading:false,
		relatedItemsResponse: {
			items,
			totalRecords,
			requestedTime,
			lastUpdated: Date.now(),
			filterApplied: {
				type: alertsSelectionState === 'CI' ? MAP_CONSTANTS.CONTEXT_SELECTION_STATES.MAP : null
			}
		},
		filteredItems: alertsSafe
	});
};

const handleGetMapAlertsFailed = coeffects => {
	LOGGING.info('handleGetMapAlertsFailed', {caller: 'handleGetMapAlertsFailed'});

	const {
		dispatch,
		action: {payload},
		updateProperties
	} = coeffects;
	dispatch(CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED,payload);
	updateProperties({cardsAreLoading: false});

};

const effectHandlers = {
	handleGetMapAlertsResponse,
	handleGetMapAlertsFailed

};

export default {
	effectHandlers
};