import {
	createNocElement, 
	createActionConfig, 
	snOpwsAlertCardActionTypes
} from '@devsnc/library-opws-mapping-shared';

import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './snMapRelatedItemsList.scss';
import view from './snMapRelatedItemsListView';
import effectOpenAlertForm from './effectOpenAlertForm';
import effectMarkCisInMap from './effectMarkCisInMap';
import effectOpenIndicatorForm from './effectOpenIndicatorForm';
import effectMarkCisInMapForIndicator from './effectMarkCisInMapForIndicator';
import {ACTION_TYPES as changeCardActionTypes} from '../sn-map-change-card/actionTypes';
import {ACTION_TYPES as indicatorTypes} from '../map-shared/indicatorTypes';

createNocElement('sn-map-related-items-list', {
	renderer: {
		type: snabbdom,
		view
	},

	styles,

	properties: {
		isLoading: {
			default: false
		},

		serviceId: {
			default: ''
		},
		
		/* For dropdown of card/item types.
		 * Each type is an object:
		 {
			 label: string
			 id: unique string
			 [optional] icon: DS icon to display on the dropdown
			 cardComponentName: string
		 }
		 */
		itemTypes: {
			default: []
		},

		/**
		 * Response objects that contains:
		 * `items` - array of item DTOs.
		 * `lastUpdated` - UNIX format tiemstamp of last updated time for the items.
		 * `totalRecords` - total number of items (in all pages).
		 */
		itemsResponse: {
			default: {}
		},

		filteredItems: {
			default: []
		},

		mapProperties: {
			default: {}
		},

		mapHistoryState: {
			default: null
		},

		displayRange: {
			default: {}
		},

		selectedCardId:{
			default: ''
		},

		topologyChangesRange: {
			schema: {type: 'number'},
			default: 0
		}
	},
	actionHandlers: {
		[effectOpenAlertForm.ACTION_TYPES.OPEN_ALERT_FORM_INVOKED]:
			createActionConfig(effectOpenAlertForm.effectHandlers.openAlertForm),

		[snOpwsAlertCardActionTypes.ALERT_CARD_SELECTED]:
			createActionConfig(effectMarkCisInMap.effectHandlers.getAffectedCis),

		[indicatorTypes.OPEN_INDICATOR_FORM_INVOKED]:
			createActionConfig(effectOpenIndicatorForm.effectHandlers.openIndicatorForm),

		[indicatorTypes.MAP_INDICATOR_CARD_SELECTED]:
			createActionConfig(effectMarkCisInMapForIndicator.effectHandlers.getSelectedCis),

		[changeCardActionTypes.TOPO_CHANGE_CARD_SELECTED]:
			createActionConfig(effectMarkCisInMap.effectHandlers.markChangeOnMap, false),

		[effectMarkCisInMap.ACTION_TYPES.MAP_GET_CIS_OF_ALERT_REQUEST]:
			createActionConfig(effectMarkCisInMap.effectHandlers.getAffectedCisRequest),

		[effectMarkCisInMap.ACTION_TYPES.MAP_GET_CIS_OF_ALERT_SUCCEEDED]:
			createActionConfig(effectMarkCisInMap.effectHandlers.handleGetCisResponse),
	}
});