import {snOpwsRelatedItemsListActionTypes} from '@devsnc/library-opws-mapping-shared';
import {TYPE_IDS} from './itemTypes';
import {set as _set, get as _get} from 'lodash';
import sendEventToMapEffect from '../map-container/sendEventToMapEffect';

const {
	NOC_EVENT_TO_MAP_SENT,
	'sa.clearAllSelections': clearAllSelections
} = sendEventToMapEffect.ACTION_TYPES;

export const clearCardSelection = dispatch => 
	dispatch(()=> ({
		type: snOpwsRelatedItemsListActionTypes.CARD_SELECTED,
		payload : {}
	}));

export const clearMapSelection = dispatch => 
	dispatch(NOC_EVENT_TO_MAP_SENT, {name: clearAllSelections});

/**
 * Clears selection and highlight states on the timeline.
 * This also re-renders the timeline component.
 * @param {Function} updateProperties 
 */
//TODO STRY50597927 reset also highlighted events
export const clearTimelineSelections = updateProperties =>
	updateProperties(() => ({
		operation: 'assign',
		path: 'timelineData',
		value: {
			selectedEvents: [],
			selectedRanges: [],
			selectedTimelineEvents: null,
			highlightedEventIds: []
		}
	}));

const clearTimelineHighlight = updateProperties =>
	updateProperties(() => ({
		operation: 'set',
		path: 'timelineData.highlightedEventIds',
		value: []
	}));
/**
 * This function centralizes the selection states in the way that the cardds list can have only one applied filter: ciFilter or selectedTimelineEvents.
 */
export const setHistoryChangesFilter = ({
	dispatch,
	updateProperties, 
	itemType = TYPE_IDS.MAP_TOPO_CHANGES,
	relatedItemIds = null,
	selectedTimelineEvents = null,
	currItemIds,
	selectedEvents = [],
	selectedRanges = []
}) => {
	//both map selection and timeline selection need to clear change cards selection as a prerequisite
	clearCardSelection(dispatch);
	
	//click on the map
	if(selectedTimelineEvents === null || selectedTimelineEvents.size === 0) {
		//TODO: due to DEF0091487, not using path for now. Need to migrate to seismic@18.2.0 and re-test
		updateProperties({
			relatedItemIds: {
				...currItemIds,
				[itemType]: relatedItemIds
			}
		});
		
		const ciFilter = _get(relatedItemIds, 'urlData.ciFilter');
		
		/**
		 * This point requires synchronization between all possible flows:
		 * * CI selected on the map: `relatedItemIds!=null` and `ciFilter!=null`: clear current tiemline selection
		 * * Empty canvas selected on the map: `relatedItemIds!=null` and `ciFilter==null`: clear timeline highlight only, keep selection
		 * * Timeline selected triggers CI de-selected on the map, then `relatedItemIds!=null` and `ciFilter==null`: not clearing timeline selection, highlight cleared.
		 * * Timeline de-selected: `relatedItemIds==null` and `ciFilter==null`: clearing timeline model
		 */
		
		if (!relatedItemIds || (relatedItemIds && ciFilter))
			clearTimelineSelections(updateProperties);

		else if (relatedItemIds && !ciFilter)
			clearTimelineHighlight(updateProperties);
		
	} else { //click on the timeline: setting timeline selection and clearing timeline highlight
		_set(currItemIds, `['${itemType}'].urlData.ciFilter`, null);
		updateProperties({relatedItemIds: currItemIds});

		updateProperties(() => ({
			operation: 'assign',
			path: 'timelineData',
			value: {
				selectedEvents,
				selectedRanges,
				selectedTimelineEvents,
				highlightedEventIds: []
			}
		}));
	}


};
