 
/**
 * For history map only.
 * When change card is selected/unselected in the side panel,
 * this effect finds and selects/unselected the change bubble on the timeline. 
 */
export const effectHighlightTimelineEvent = coeffects => {
	const {
		action: {
			payload: {
				isSelected,
				item
			}
		},
		properties: {
			isHistory
		},
		updateProperties,
	} = coeffects;

	if (!isHistory)
		return;

	//currently supporting highlight of 1 event from 1 card.
	const highlightedEventIds = isSelected ? [item.id] : [];
	
	updateProperties(() => ({
		operation: 'set',
		path: 'timelineData.highlightedEventIds',
		value: highlightedEventIds
	}));
};