import {onNextFrame, snOpwsRelatedItemsListActionTypes} from '@devsnc/library-opws-mapping-shared';
import {markCisOnMap} from '../sn-map-related-items-list/effectMarkCisInMap';
import {TYPE_IDS} from '../map-shared/itemTypes';
import {setHistoryChangesFilter, clearMapSelection} from '../map-shared/setHistoryChangesFilter';

const ACTION_TYPES = {
	TIMELINE_DATA_SELECTED_UPDATED: 'TIMELINE_CHART#DATA_SELECTED'
};

/**
 * This effect handles selection of event on the timeline
 */
const onTimelineEventSelection = async coeffects => {
	const {
		action,
		dispatch,
		updateProperties,
		properties: {
			relatedItemIds
		}
	} = coeffects;
	
	clearMapSelection(dispatch);

	await onNextFrame();

	const {
		selectedEvents,
		selectedRanges
	} = action.payload || {};

	const ciIds = selectedEvents.map(event => event.data.ciId);
	markCisOnMap(dispatch, ciIds);
	
	const selectedTimelineEvents = new Set(selectedEvents.map(event => event.data.id));

	markCards({
		dispatch, 
		updateProperties, 
		selectedTimelineEvents,
		relatedItemIds, 
		selectedEvents,
		selectedRanges
	});
};

const markCards = ({
	dispatch, 
	updateProperties, 
	selectedTimelineEvents,
	relatedItemIds, 
	selectedEvents,
	selectedRanges
}) => {
	setHistoryChangesFilter({
		dispatch,
		updateProperties, 
		typeId: TYPE_IDS.MAP_TOPO_CHANGES, 
		selectedTimelineEvents, 
		currItemIds: relatedItemIds, 
		selectedEvents,
		selectedRanges
	});

	dispatch(({state})=> ({
		type: snOpwsRelatedItemsListActionTypes.REFRESH_REQUESTED,
		payload: {
			...state.relatedItemsRefreshParams,
			forceRefresh: true
		}
	}));
};

const effectHandlers = {
	onTimelineEventSelection
};

export default {
	ACTION_TYPES,
	effectHandlers
};