import {t} from 'sn-translate';
import {isFunction as _isFunction} from 'lodash';
import MAP_CONSTANTS from '../map-shared/constants';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {stringify} from './json';
import sendEventToMapEffect from './sendEventToMapEffect';

export const BLOCKING_MESSAGE = t(`It may take several minutes to load. Try using CI list instead. 
To always display this map, disable the Hide large map option.`);


export const syncBlockingState = ({graphDTOData, updateState}) => {
	if (!graphDTOData || !_isFunction(graphDTOData.isMapBlocked)) {
		LOGGING.warn(`saveBlockingReason: Unexpected dataType received, expected to have function 'isMapBlocked':
		${stringify(graphDTOData)}`);
		return;
	}
	
	const isBlocked = graphDTOData.isMapBlocked();

	updateState({
		isBlocked,
		loadingState: isBlocked ? MAP_CONSTANTS.LOADING_STATES.BLOCKED : MAP_CONSTANTS.LOADING_STATES.DONE,
		blockingState: isBlocked ? {
			reason: graphDTOData.getBlockingReason(),
			url: graphDTOData.getListViewURL()
		} : {}
	});
};

export const openListView = ({serviceId, blockingState}) => () =>
	window.open(blockingState.url, `${serviceId}_list_view`);

export const showBlockedMap = ({dispatch, updateState, rememberForSession = true}) => () => {
	updateState({
		loadingState: MAP_CONSTANTS.LOADING_STATES.LOADING,
		isBlocked: false
	});

	dispatch(() => ({
		type: sendEventToMapEffect.ACTION_TYPES.NOC_EVENT_TO_MAP_SENT,
		payload: {
			name: 'sa.refreshMap', 
			args: [{showLargeMap: Boolean(rememberForSession)}]
		}
	}));

};