import {LOGGING} from '@devsnc/library-opws-mapping-shared';

const SYS_PROP_MAX_HISTORY_CHANGES_DISPLAYED = 'itom_map_app.max_history_changes_displayed';

const SYS_PROPERTIES = {
	[SYS_PROP_MAX_HISTORY_CHANGES_DISPLAYED](value) {
		const parsedValue = Number.parseInt(value);
		const sanitizedValue = Math.max(isNaN(parsedValue) ? 1000 : parsedValue, 1);
		if(Number(value) !== sanitizedValue)
			LOGGING.log(`sys_property ${SYS_PROP_MAX_HISTORY_CHANGES_DISPLAYED} has value ${value}, sanitized to be ${sanitizedValue}`, {caller: 'sn-service-mapping:SYS_PROPERTIES'});
		return sanitizedValue;
	}
};

export default {
	SN_MAP_CONNECTED: 'SN_MAP_CONNECTED',

	NOC_EVENT_FROM_MAP_ARRIVED: 'NOC_EVENT_FROM_MAP_ARRIVED',
	NOC_EVENT_TO_MAP_SENT: 'NOC_EVENT_TO_MAP_SENT',

	NOC_MAP_APP_COMPONENT_ID: 'noc-map-app-root',
	MAP_APP_NOTIFICATIONS_COMPONENT_ID_BASE: 'map-app-notifications',

	MAP_HISTORY_STATES: {
		CURRENT: 'CURRENT_VIEW',
		HISTORY: 'HISTORY_VIEW',
		COMPARE: 'COMPARE_VIEW'
	},

	CI_PROPERTY_CHANGE_STATES:{
		REMOVED: 'REMOVED',
		ADDED: 'ADDED',
		MODIFIED: 'MODIFIED'
	},

	SYS_PROPERTIES,

	SYS_PROP_MAX_HISTORY_CHANGES_DISPLAYED,
	

	/**
	 * send to angular map and update about history mode.
	 * {
	 * 		isCompare
	 * 		from: UNIX format, when isCompare is true
	 * 		to: UNIX format, when isCompare is true
	 * 		timestamp: UNIX format, when isCompare is false
	 * 		checkpoints: array of change ids in current displayRange
	 * }
	 */
	EVT_WS_SET_MAP_HISTORY_STATE: 'EVT_WS_SET_MAP_HISTORY_STATE',

	/**
	 * Enum of possible states when filtering contextual panel.
	 */
	CONTEXT_SELECTION_STATES: {
		MAP: 'MAP', //map element is selected (CI or connection)
		TIMELINE: 'TIMELINE' //change/event is selected on the timeline.
	},

	/**
	 * Enum of loading states of map. 
	 * Values are compatible with now-empty-state.type
	 */
	LOADING_STATES: {
		ERROR: 'loading-error',
		DONE: null,
		LOADING: 'loading',
		BLOCKED: 'empty'
	}
};

