import {ITEM_TYPES, TYPE_IDS, HISTORY_ITEM_TYPES} from '../map-shared/itemTypes';
import {ACTION_TYPES as mapSettingsMenuBehaviorActionTypes} from '../map-settings-menu-behavior/mapSettingsMenuBehavior';
import {INDICATOR_IDS, INDICATOR_TYPES} from '../map-shared/indicatorTypes';
import {map as _map, memoize as _memoize} from 'lodash';
import MAP_CONSTANTS from '../map-shared/constants';

const ACTION_TYPES = {
	MAP_SETTINGS_SET: mapSettingsMenuBehaviorActionTypes.MAP_SETTINGS_SET
};

/**
 * Prepare base static part of item types.
 * The condition isHistory will bring only change cards, isEm=false will filter out Alerts panel.
 */
const getBaseItemTypes = _memoize((isEm, isHistory) => {
	if (isHistory)
		return HISTORY_ITEM_TYPES;

	return isEm ? ITEM_TYPES : ITEM_TYPES.filter(item => item.id !== TYPE_IDS.ALERTS);
}, (isEm, isHistory) => `${isEm}_${isHistory}`);


const buildIndicatorType = ({settingsModel}, indicatorId) => {
	const indicator = settingsModel[indicatorId];

	if (!indicator || !indicator.enabled)
		return null;

	return INDICATOR_TYPES.find(indicatorType => (indicatorType.id === indicatorId));
};

/**
 * On each change in `mapSettings`, use the model to recompute `relatedItemTypes` property.
 * `relatedItemTypes` is a selectable property that is used by cards container on the side panel.
 */
const effectComputeRelatedTypes = coeffects => {
	const {
		state: {
			mapSettings
		},
		properties: {
			mapHistoryState,
			isEm = true
		},
		updateProperties
	} = coeffects;
	
	if (!mapSettings || !mapSettings.model)
		return;

	const isHistory = MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT !== mapHistoryState;

	const indicatorTypeBuilder = buildIndicatorType.bind(null, {settingsModel: mapSettings.model});
	const indicatorTypes = isHistory ? [] : _map(INDICATOR_IDS, indicatorTypeBuilder).filter(Boolean);

	const baseItemTypes = getBaseItemTypes(isEm, isHistory);

	const relatedItemTypes = [
		...baseItemTypes,
		...indicatorTypes
	];

	updateProperties({relatedItemTypes});
};


const effectHandlers = {
	effectComputeRelatedTypes
};

export default {
	ACTION_TYPES,
	effectHandlers
};