import {t} from 'sn-translate';
import {createRef} from '@servicenow/ui-renderer-snabbdom';
import MAP_CONSTANTS from '../map-shared/constants';
import {initEventBus} from './eventBus';
import {getTimelineJsx, getHeaderFieldsJsx} from './topAreaJsx';
import '../map-header-panel';
import '../map-side-panel';
import '@servicenow/now-empty-state';
import '@servicenow/now-button';
import '@devsnc/library-opws-mapping-shared'; //sn-opws-is-visible
import  effectOnMapVisibilityChanges from './effectOnMapVisibilityChanges';
import {BLOCKING_MESSAGE, openListView, showBlockedMap} from './mapBlocking';

const LABEL_MAIN = t('Service map tab');
const LABEL_LOADING = t('Loading map');
const LABEL_LOADING_HISTORY = t('Loading history view');
const LABEL_MAP_FRAME = t('Service map');
const LABEL_OPEN_LIST_VIEW = t('Open CI list');
const LABEL_LOAD_BLOCKED_MAP = t('View this map now');

const onIframeLoad = ({dispatch, iframeRef, mapId}) => () =>
	initEventBus(dispatch, iframeRef, mapId);

const getLoadingStateJsx = ({serviceId, isBlocked, blockingState, loadingState, isHistory, dispatch, updateState}) => {
	const loadingText = isHistory ? LABEL_LOADING_HISTORY : LABEL_LOADING;
	const loadingProps = (loadingState === MAP_CONSTANTS.LOADING_STATES.LOADING) ?  {
		headerText: loadingText,
		bodyText: false
	} : isBlocked ? {
		headerText: blockingState.reason,
		bodyText: BLOCKING_MESSAGE
	} : {};

	if (isBlocked) {
		return (
			<div className='full-size fg now-p-block-start--3xl'>
				<now-empty-state variant='medium' type={loadingState} {...loadingProps}/>

				<div className='now-align -inline-center empty-state-buttons' >
					<now-button 
						className='now-align -inline-start'
						on-click={openListView({serviceId, blockingState})} 
						label={LABEL_OPEN_LIST_VIEW} 
						variant='primary'
						size='sm'
					/>

					<now-button 
						className='now-align -inline-end now-m-inline-start--sm'
						on-click={showBlockedMap({dispatch, updateState})} 
						label={LABEL_LOAD_BLOCKED_MAP} 
						variant='secondary'
						size='sm'
					/>
				</div>
				
			</div>
		);
	}

	return loadingState ? (
		<div className='full-size fg'>
			<now-empty-state variant='fit' type={loadingState} {...loadingProps}/>
		</div>
	) : null;
};

export default (state, {dispatch, updateState}) => {
	const {
		isInitializing,
		loadingState,
		isBlocked,
		blockingState,
		mapInitialData,
		mapSettings,
		serviceSeverity,
		componentId,
		properties: {
			url, 
			serviceId, 
			mapId,
			isEm,
			isHistory,
			notifications = [],
			mapHistoryState,
			timelineData
		}
	} = state;

	if (!url || !serviceId)
		return null;

	const iframeRef = createRef();
	const timelineJsx = isInitializing ? null : getTimelineJsx({mapHistoryState, timelineData});
	const headerFieldsJsx = isInitializing ? null : getHeaderFieldsJsx({mapInitialData, isEm, serviceSeverity, mapHistoryState});
	const extraSpacingAfterHeading = headerFieldsJsx ? '' : 'now-m-block-end--sm';

	const loadingStateJsx = getLoadingStateJsx({serviceId, loadingState, isBlocked, blockingState, isHistory, dispatch, updateState});

	const bodyJsx = mapId ? (
		<div className='main-section full-size now-align'>
			<div className='left-side full-size now-align -inline-start'>
				{timelineJsx}

				<div className='map-container full-size now-align -block-end'>

					{loadingStateJsx}

					<iframe className='map-frame full-size bg'
						key='ng_map_iframe'
						ref={iframeRef}
						title={LABEL_MAP_FRAME}
						on-load={onIframeLoad({dispatch, iframeRef, mapId})}
						src={url}
					/>
				</div>
			</div>
	
	
			<map-side-panel 
				map-component-id={componentId} 
				is-loading={isInitializing}
				is-blocked={isBlocked}
				map-history-state={mapHistoryState}
			/>
		</div>
	) : null;

	const anchorElm = createRef();

	return (
		<main className='layout full-size now-align' aria-label={LABEL_MAIN}>

			<sn-opws-is-visible anchor-elm={anchorElm} action-on-change={effectOnMapVisibilityChanges.ACTION_TYPES.MAP_VISIBILITY_CHANGE} />
			
			<map-header-panel
				className={extraSpacingAfterHeading}
				service-id={serviceId}
				map-id={mapId}
				is-history={isHistory}
				is-em={isEm}
				service-severity={serviceSeverity}
				map-initial-data={mapInitialData}
				map-settings={mapSettings}
				notifications={notifications}
				is-loading={isInitializing}
				is-blocked={isBlocked}
				ref={anchorElm}
			/>
			
			{headerFieldsJsx}
			{bodyJsx}
		</main>
	);
};