import effectGetMapChangeItems from '../map-container/effectGetMapChangeItems';
export const setChangeCardsinHistory = coeffects => {
	const {
		dispatch,
		properties: {
			displayChanges,
			timelineData: {
				selectedTimelineEvents
			}
		}
	} = coeffects;
	const changes = selectedTimelineEvents !== null ?
		displayChanges.filter(change => selectedTimelineEvents.has(change.id)):
		displayChanges;


	dispatch(effectGetMapChangeItems.ACTION_TYPES.GET_HISTORY_CHANGES_REQUEST_SUCCEEDED, changes);
};