import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';

export const computed = state => {
	const {serviceId, isEm, isHistory} = state.properties;

	if (!serviceId)
		return '';

	const uriBase = CONSTANTS.GLOBALS.BS_URL.replace('{0}', serviceId)
		.replace('{1}', isEm ? CONSTANTS.GLOBALS.ASSURANCE : CONSTANTS.GLOBALS.MAPPING);

	const uri = `${uriBase}&sysparm_is_ws=true${isHistory ? '&sysparm_is_ws_history=true' : ''}`;
	return uri;
};