import '@devsnc/library-opws-mapping-shared'; //sn-opws-related-items-list
import {getSidePanelTitle, getSidePanelHeaderJsx} from '../map-side-panel/getSidePanelHeader';
import MAP_CONSTANTS from '../map-shared/constants';
import {getAltFooterBuilder} from './getAltFooterBuilder';

//view()
export default (state, {dispatch}) => {
	const {
		componentId: wrapperComponentId = '',
		properties: {
			itemsResponse: {
				lastUpdated = 0,
				totalRecords = 0,
				items = [],
				serviceId = '',
				filterApplied
			},
			filteredItems,
			itemTypes = [],
			mapProperties = null,
			mapHistoryState,
			isLoading,
			displayRange,
			selectedCardId,
			topologyChangesRange
		}
	} = state;
	
	const componentId = `${wrapperComponentId}_opws`;
	const isFilterActive = Boolean(filterApplied && filterApplied.type);

	const headerTitle = getSidePanelTitle({
		isFilterActive, 
		isPropertiesPanel: false, 
		mapHistoryState
	});
	
	const sidePanelHeaderJsx = getSidePanelHeaderJsx({
		serviceId, 
		items: filteredItems, 
		mapProperties, 
		mapHistoryState, 
		dispatch, 
		filterApplied, 
		displayRange
	});

	const sidePanelHeaderWrapperJsx = sidePanelHeaderJsx ? (
		<div slot='header-extra-content' className='now-align flex-full-width now-p-block-end--xl'>
			{sidePanelHeaderJsx}
		</div>
	) : null;

	const altFooterTextBuilder = getAltFooterBuilder({items, mapHistoryState, topologyChangesRange});

	return (
		<sn-opws-related-items-list
			manage-selected-item-id
			selected-item-id={selectedCardId}
			component-id={componentId}
			is-loading={isLoading}
			items={items}
			last-updated={lastUpdated}
			total-records={totalRecords}
			item-types={itemTypes}
			header-title={headerTitle}
			show-back-button={isFilterActive}
			hide-refresh-button={mapHistoryState !== MAP_CONSTANTS.MAP_HISTORY_STATES.CURRENT}
			alt-footer-text-builder={altFooterTextBuilder}
		>

			{sidePanelHeaderWrapperJsx}
		</sn-opws-related-items-list>
	);
	
};