import {createCardComponent, generateViewFn} from '@devsnc/library-opws-mapping-shared';
import {getIncidentCardContentJsx as getCardContentJsx} from './getIncidentCardContentJsx';
import {ACTION_TYPES, CARD_COMPONENTS, INDICATOR_IDS} from '../map-shared/indicatorTypes';
import styles from './snMapIncidentCard.scss';

const view = generateViewFn({
	getCardContentJsx,
	openFormActionType: ACTION_TYPES.OPEN_INDICATOR_FORM_INVOKED,
	cardSelectedActionType: ACTION_TYPES.MAP_INDICATOR_CARD_SELECTED,
	footerFieldName: 'sysUpdatedOn',
	descriptionFieldName: 'shortDescription'
});

/**
 * Seismic component `sn-map-incident-card`.
 * Card for map topology changes to show on side panel on nocpit and on map.
 **/
createCardComponent({
	componentName: CARD_COMPONENTS[INDICATOR_IDS.MAP_INCIDENT],
	styles,
	view
});