import moment from 'moment';
import MAP_CONSTANTS from '../map-shared/constants';


export const getEmptyTimelineData = () => ({
	events: [
		//events are topology changes
	],

	ranges: [
		//ranges are severity changes
	],

	//both ranges are objects with {start: <long>, end: <long>}
	totalRange: null,
	displayRange: null,

	//Js:Set() of IDs of selected timeline events, stores state of timeline selection filter.
	selectedTimelineEvents: null,
	//manage-selections is true, storing selected items here
	selectedEvents: [],
	selectedRanges: [],


	//IDs of highlighted events.
	//This Set allows fast reset of highlighted event w/o full traversal over all visible events.
	//Use _eventsIndex to fetch event object.
	highlightedEventIds: [],

	//Map from change object in displayChanges to event object in timelineData.events
	//this structure allows O(1) access from change card to change icon on the timeline.
	_eventsIndex: new Map()
});

export const isHistoryMap = mapHistoryState => 
	(mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.HISTORY || mapHistoryState === MAP_CONSTANTS.MAP_HISTORY_STATES.COMPARE);

/**
 * Given array of history changes for the current displayRange,
 * Returns a value ofor mapHistoryState.
 * @param {*} param0 
 * @param {'HISTORY' | 'COMPARE'} see MAP_CONSTANTS.MAP_HISTORY_STATES
 */
export const resolveMapHistoryState = ({displayChanges}) => 
	!displayChanges || displayChanges.length === 0
		? MAP_CONSTANTS.MAP_HISTORY_STATES.HISTORY
		: MAP_CONSTANTS.MAP_HISTORY_STATES.COMPARE;

const escapeNaN = n => isNaN(Number(n)) ? -1 : Number(n);

/**
 * Escaping of timeline range limits.
 * Timeline range cannot be before the service creation after, and acnnot be in future.
 * start date should be always before the end date.
 * @param {*} param0 
 * @return {{start: Number, end: Number}} - the sanitized range
 */
export const sanitizeHistoryRange = ({start, end, serviceCreationTime, upperDateLimit}) => {
	const fixedStart = Math.max(serviceCreationTime, escapeNaN(start));
	const fixedEnd = Math.min(upperDateLimit, escapeNaN(end));

	return {
		start: Math.min(fixedStart, fixedEnd),
		end: Math.max(fixedStart, fixedEnd)
	};
};

export const isRangeIncluded = (included, wrapping) => {
	//input validation
	if (!included || !wrapping || wrapping.start < 0 || included.start < 0)
		return false;

	return included.start >= wrapping.start && included.end <= wrapping.end;
};

/**
 * Time range formatter for debug logs.
 * @param {*} start a timestamp or Date
 * @param {*} end a timestamp or Date
 */
export const formatRange = (start, end) => {
	const mStart = moment(start);
	const mEnd = moment(end);
	const duration = moment.duration(mEnd.diff(mStart));

	return `${duration.humanize()}: ${mStart.format('MMM-DD HH:mm:ss')} - ${mEnd.format('MMM-DD HH:mm:ss')}`;
};

/**
 * Forcing timeline refresh (rendering) is useful after the contianer has changed its size or visibility,
 * and the timeline needs to resize itself.
 * @param updateProperties {Function} - must be the updateProperties function for the map-container component.
 */
export const forceTimelineRefresh = updateProperties =>
	updateProperties(({properties}) => {
		const {highlightedEventIds} = properties.timelineData;
		return {
			operation: 'assign',
			path: 'timelineData',
			value:{
				//adding meaningless item to highlightedEventIds t otrigger difference in the timeline input props.
				highlightedEventIds: [...highlightedEventIds, -1]
			} 
		};
	});