import mapSettingsModifiedEffect from '../map-settings-menu-behavior/mapSettingsModifiedEffect';

/**
 * The key in this mapping MUST be the name of event on the angular side.
 * This obejct as filter for events that will pass through from angular eventBus to the seismic side.
 * The lookup performed by key!
 */
const WHITE_LIST = {
	//this event sends callback from map that should be called with service name.
	//that replaces 'sysparm_bsname' parameter in URL (the name retrieved async, so it cannot be set in URL w/o delaying map load).
	EVT_WS_GET_SERVICE_NAME: 'EVT_WS_GET_SERVICE_NAME',

	/**
	 * Dispatched once from angular map to transfer getter function that will return severty color for each severity code.
	 */
	EVT_WS_SEVERITY_COLORS_GETTER_SENT: 'EVT_WS_SEVERITY_COLORS_GETTER_SENT',

	/**
	 * `snc.layoutReapplied'` is fired by map when new data arrived and procesed by the layout (ready to draw state).
	 * When relying on data coming fro mthe map model, this is the ideal even to listen to see that map is ready in current state.
	 * Used here to sync first loading of the map.
	 */
	'snc.layoutReapplied': 'snc.layoutReapplied',

	//listen for DASHBOARD timestamp to fetch up-to-date severity of service
	sa_status_change: 'sa_status_change',

	//element selected on map
	// 'snc.selectionUpdated': 'snc.selectionUpdated',

	//new data received for properties panel
	'sa.refreshPropertiesPane': 'sa.refreshPropertiesPane',


	/**
	 * isLoaded {Boolean} - true if map succesfully loaded.
	 * isFailed {Boolean} - true if map loading have failed and stopped.
	 */
	wsMapLoaderStateChanged: 'wsMapLoaderStateChanged',

	/**
	 * Map loading was blocked
	 * graphDTOData - instance of sncGraphType with APIs isMapBlocked() and getBlockingReason()
	 * Additional API getBlockingMessage() returns message for angular map and not used in Workspace.
	 */
	'snc.mapBlockingStateChanged': 'snc.mapBlockingStateChanged',

	/**
	 * Dispatched when "hide large map" toggle was modified.
	 * state {Boolean} - updated toggle state
	 */
	'sa.blockLargeServiceChanged': 'sa.blockLargeServiceChanged',

	/** 
	 * notifies on each change in alerts panel state
	 * {
			urlData: {timestamp: String, params: {sysparm_table, sysparm_query}}
			sysIds: [sysIds for em_alert_history]
		}
	 */
	EVT_WS_ALERT_PANEL_UPDATE: 'EVT_WS_ALERT_PANEL_UPDATE',

	/** 
	 * notifies on each change in 'history changes' panel state
	 * {
			urlData: {params: {sysparm_table, sysparm_query}}
			sysIds: {sysIds for svc_model_checkpoint_attr => {id, ciId, timestamp}}
		}
	 */
	EVT_WS_HISTORY_CHANGES_PANEL_UPDATE: 'EVT_WS_HISTORY_CHANGES_PANEL_UPDATE',

	/**
	 * notifies on each change in 'indicators/mashups' panels state
	 * {
			urlData: {params: {sysparm_table, sysparm_query, sysparm_cmdb_ci_field}} 
			sysIds: [cmdb_ci ids to filter by]
		}
	 */
	EVT_WS_INDICATOR_PANEL_UPDATE: 'EVT_WS_INDICATOR_PANEL_UPDATE',

	...mapSettingsModifiedEffect.MAP_EVENT_NAMES
};

export default WHITE_LIST;