import {isArray as _isArray} from 'lodash';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import timelineConstants from './timelineConstants';
import MAP_CONSTANTS from '../map-shared/constants';

const {TIMELINE_ITEM_TYPES} = timelineConstants;

const ACTION_TYPES = {
	MAP_HISTORY_DATA_REQUEST: 'MAP_HISTORY_DATA_REQUEST',
	MAP_HISTORY_DATA_REQUEST_SUCCEEDED: 'MAP_HISTORY_DATA_REQUEST_SUCCEEDED',
	MAP_HISTORY_DATA_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED,
	MAP_HISTORY_DATA_SET: 'MAP_HISTORY_DATA_SET'
};

const getHistoryData = createHttpEffect('/api/sn_itom_map_app/embedded_map/timeline', {
	method: 'GET',
	batch: false,
	queryParams: ['serviceId', 'startTime', 'endTime', 'showSeverity'],
	successActionType: ACTION_TYPES.MAP_HISTORY_DATA_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.MAP_HISTORY_DATA_REQUEST_FAILED
});

/**
 * Reducer function to run with reduce on timelineData response.
 * @param {*} timelineData - {changes: {}, severities: {}}.
 * * lastSeverityTimestamp - holds timestamp of latest bubble of type SEVERITY.
 * * Changes are mapped by bubble change, transformed later to the array of categories.
 * * Severities are mapped by severity codes here,  transformed later to the array of categories.
 * @param {*} bubble
 */
const reduceTimelineData = ({changes, severities, prevTimestamp, serviceCreationTime}, bubble) => {
	const {type, time: timestamp} = bubble;

	const next = {
		changes,
		severities,
		prevTimestamp,
		serviceCreationTime
	};

	if (type === TIMELINE_ITEM_TYPES.SEVERITY) {
		next.severities.push({
			data: bubble,
			start: timestamp,
			end: prevTimestamp
		});

		return {
			...next,
			prevTimestamp: timestamp
		};
	}

	//else: a timeline event

	//use service creation bubble to update serviceCreationTime, and don't add it on the timeline changes
	if (type === TIMELINE_ITEM_TYPES.BS_CREATED)
		next.serviceCreationTime = timestamp;

	else
		next.changes.push(bubble);

	return next;
};

const historyDataResponse = coeffects => {
	const {
		action: {payload},
		dispatch,
		updateProperties,
		updateState,
		properties: {
			serviceCreationTime: storedServiceCreationTime
		}
	} = coeffects;

	if (checkForError({payload, dispatch, caller: 'historyDataResponse'})) 
		return;

	const {
		requestedRange,
		historyData
	} = payload;

	if (!_isArray(historyData)) {
		LOGGING.error('Expected array of historyData from the timeline API. Skipping...', {caller: 'historyDataResponse'});

		updateState({loadingState: MAP_CONSTANTS.LOADING_STATES.ERROR});
		return;
	}
		

	const rawTimelineData = historyData.reduceRight(reduceTimelineData, {
		changes: [],
		severities: [],
		//TODO `end` of the last range should be set to `displayEndDate` or `endDate` of the timeline range
		prevTimestamp: Number.POSITIVE_INFINITY,
		serviceCreationTime: storedServiceCreationTime
	});

	const {changes, severities, serviceCreationTime} = rawTimelineData;

	//both arrays are flipped because of `reduceRight`.
	//restore original order by time
	changes.reverse();
	severities.reverse();

	updateProperties({
		currDataRange: requestedRange,
		allChanges: changes,
		severityRanges: severities,
		serviceCreationTime
	});

	dispatch(() => ({type: ACTION_TYPES.MAP_HISTORY_DATA_SET}));
};

const effectHandlers = {
	getHistoryData,
	historyDataResponse
};

export default {
	ACTION_TYPES,
	effectHandlers
};
