import {
	getIndicatorCardWithAvatarJsx, 
	getAvatarElementJsx, 
	getLabelValueElementJsx
} from '@devsnc/library-opws-mapping-shared';

export const getProblemCardContentJsx = item => {
	const {
		assignedTo,
		assignmentGroup,
		cmdbCi,
		relatedIncidents
	} = item;

	const items = [
		{jsxBuilder: getAvatarElementJsx, field: assignedTo},
		{jsxBuilder: getLabelValueElementJsx, field: assignmentGroup},
		{jsxBuilder: getLabelValueElementJsx, field: cmdbCi},
		{jsxBuilder: getLabelValueElementJsx, field: relatedIncidents},
	];

	const cardJsx = getIndicatorCardWithAvatarJsx(items);
	return (
		<div>
			{cardJsx}
		</div>
	);
};