import '@servicenow/now-label-value';
import '@servicenow/now-heading';
import {isArray as _isArray, isString as _isString, values as _values} from 'lodash';
import {t} from 'sn-translate';
import {getBackButtonJsx} from '@devsnc/library-opws-mapping-shared';
import {getSidePanelTitle, getSidePanelHeaderJsx, getElementType, isEndpoint} from '../../getSidePanelHeader';
import actionTypes from './actionTypes';
import MAP_CONSTANTS from '../../../map-shared/constants';

const EMPTY = t('Empty');

const {CI_PROPERTY_CHANGE_STATES} = MAP_CONSTANTS;

const createLabelValue = (items, sectionId) => {
	const newValues = items.values.map(value => ({
		value: value.value,
		type: 'string',
		...(value.previous ? {previous: value.previous} : {})
	}));

	const edsItems = [
		{
			label: items.label,
			value: newValues
		}
	];

	const key = `${sectionId}_${items.label}`;


	return (
		<span key={key} className='item-wrapper'>
			<now-label-value-stacked
				className='item'
				size='md'
				variant='primary'
				align='vertical'
				wrapped={true}
				items={edsItems} />
		</span>
	);
};

const prepareClusterNodes = (items, serviceId, dispatch) => (
	items.map(item =>
		<div className='now-align cluster-node-value'>
			<now-text-link
				className='now-align -block-start link now-p-block--xs'
				label={item.linkLabel}
				href='javascript:void(0)'
				on-click={openForm({sysId: item.sysId, ciType: item.type, serviceId, dispatch})}
				variant='secondary' />

			<now-label-value-inline className='now-align -block-end' label={item.hostName} />

		</div>
	)
);

const createLabelValueWithLink = (data, dispatch, sectionId) => {
	const {label, items, serviceId} = data;
	const key = `${sectionId}_${label}`;
	return (
		<span key={key} className='item-wrapper cluster-node'>
			<now-label-value-inline label={label} />
			{prepareClusterNodes(items, serviceId, dispatch)}
		</span>
	);
};

const buildValues = value => (
	{
		type: 'string',
		value
	}
);

const createSectionData = (meta, data) => {
	const {serviceId, dispatch, sectionId} = meta;
	const {
		value,
		label,
		oldValue: previous,
		changeState,
		isLink = false
	} = data;
	if ((!value && !previous) || !label)
		return null;

	if (isLink)
		return prepareLabelValueWithLink(serviceId, label, value, dispatch, sectionId);

	const changeType = CI_PROPERTY_CHANGE_STATES[changeState];

	if (changeType) {
		const items = {
			label,
			values: [
				{
					value: getItemValue(changeState, value),
					previous: getItemPrevious(changeState, previous)
				}
			]
		};
		return createLabelValue(items, sectionId);
	}

	if (_isString(value)) {

		const items = {
			label,
			values: [
				{
					value
				}
			]
		};
		return createLabelValue(items, sectionId);
	}

	if (_isArray(value)) {
		const values = value.map(buildValues);
		const items = {
			label,
			values
		};
		return createLabelValue(items, sectionId);
	}
};

const getItemValue = (changeState, value) => {
	if (changeState === CI_PROPERTY_CHANGE_STATES.REMOVED)
		return EMPTY;
	return value;
};

const getItemPrevious = (changeState, previous) => {
	if (changeState === CI_PROPERTY_CHANGE_STATES.ADDED)
		return EMPTY;
	return previous;
};

const prepareLabelValueWithLink = (serviceId, label, value, dispatch, sectionId) => {
	if (_isString(value)) {
		const item = {
			serviceId,
			label,
			items: [
				{
					sysId: value.sysId,
					type: value.type,
					linkLabel: value.name,
					hostName: value.hostName
				}
			]
		};
		return createLabelValueWithLink(item, dispatch, sectionId);
	}

	if (_isArray(value)) {
		const item = {
			serviceId,
			label,
			items:
				value.map(value => ({
					sysId: value.sysId,
					type: value.type,
					linkLabel: value.name,
					hostName: value.hostName
				}))
		};
		return createLabelValueWithLink(item, dispatch, sectionId);
	}
};

const generateSection = (meta, section) => {
	const {sys_id: sectionId} = section;
	const sectionItems = _values(section.data).map(createSectionData.bind(null, {...meta, sectionId}));
	return (
		<span className='section-wrapper'>
			<now-heading
				className='section-header now-p-block-end--sm'
				label={section.name}
				level='3'
				variant='title-primary'
				has-no-margin />

			{sectionItems}
		</span>
	);
};

/**
 * Opens secondary child tab for CI form under the primary service tab
 * @param {*} sysId 
 * @param {*} className 
 * @param {*} dispatch 
 */
const openForm = ({sysId, ciType, serviceId, dispatch}) => () => {
	dispatch('ITEM_SELECTED', {
		table: ciType,
		sys_id: sysId,
		parent_table: 'cmdb_ci_service_discovered',
		parent_sys_id: serviceId
	});
};

//view()
export default (state, {dispatch}) => {
	const {
		mapProperties,
		serviceId,
		mapHistoryState,
		displayRange
	} = state.properties;

	const {
		sections = []
	} = (mapProperties || {});
	
	const isElementSelected = Boolean(getElementType(mapProperties || {}) || isEndpoint({mapProperties}));
	const filterApplied = isElementSelected ? {type: MAP_CONSTANTS.CONTEXT_SELECTION_STATES.MAP} : null;

	const panelHeaderJsx = getSidePanelHeaderJsx({serviceId, mapProperties, mapHistoryState, dispatch, filterApplied, displayRange});
	const sectionsMap = sections.map(generateSection.bind(null, {serviceId, dispatch}));
	const sectionJsx = (
		<div className='content'>
			{sectionsMap}
		</div>
	);

	const isFilterActive = Boolean(filterApplied && filterApplied.type);

	const heading = getSidePanelTitle({ 
		isFilterActive,
		isPropertiesPanel: true,
		mapHistoryState
	});

	const backButton = getBackButtonJsx({
		dispatch, 
		showBackButton: isFilterActive, 
		actionType: actionTypes.BACK_ACTION
	});

	const headingJsx = heading ? (
		<now-heading
			className='now-align -inline-end'
			label={heading}
			variant='header-secondary'
			level='2'
			has-no-margin
		/>
	) : null;

	const panelHeaderWrapperJsx = panelHeaderJsx ? (
		<div className='now-align -block-end flex-full-width now-m-block-start--xl'>
			{panelHeaderJsx}
		</div>
	) : null;

	return (
		<section className='map-properties now-align flex-column'>
			<div className='now-align -block-start flex-column'>
				<div className='now-align -block-start'>
					{backButton}
					{headingJsx}
				</div>
				{panelHeaderWrapperJsx}
			</div>
			
			{sectionJsx}
		</section>
	);
};