import {createActionConfig} from '@devsnc/library-opws-mapping-shared';
import MAP_CONSTANTS from '../map-shared/constants';
import effectInitHistoryMode from './effectInitHistoryMode';
import effectReceiveHistoryData from './effectReceiveHistoryData';
import effectResolveHistoryState from './effectResolveHistoryState';
import effectOnTimelineRangeUpdated from './effectOnTimelineRangeUpdated';
import {effectHighlightTimelineEvent} from './effectHighlightTimelineEvent';
import effectOnTimelineEventSelection from './effectOnTimelineEventSelection';
import {ACTION_TYPES as ACTION_TYPES_CHANGE_CARD_SELECTED} from '../sn-map-change-card/actionTypes';
import {getEmptyTimelineData} from './timelineFunctors';

const {MAP_HISTORY_STATES} = MAP_CONSTANTS;

export const behaviorServiceHistory = {
	name: 'behaviorServiceHistory',
	
	properties: {
		/**
		 * Initial range of the timeline when the history map is openend.
		 * If unset, will be automatically set on load.
		 */
		initialRange: {
			default: null
		},

		//last date that timeline can go forward to.
		//locked on end date of the initialRange, fallback to Date.now()
		upperDateLimit: {
			computed({properties: {initialRange}}) {
				const {end: upper = Date.now()} = initialRange || {};
				return upper;
			}
		},

		/**
		 * Current state of the map from the time dimension perspective.
		 * The value must be one of the defined in map-shared/constants/MAP_HISTORY_STATES
		 */
		mapHistoryState: {
			default: MAP_HISTORY_STATES.CURRENT,
			selectable: true
		},

		/**
		 * Provides statistics about number of topology chnges for different time ranges back fro mcurrent time.
		 * The array is sorted by relevance of the ranges.
		 * The range is ms back since current time. For example 1 hour is: 1000 * 60 * 60 = 3600000
		 * [{range: ms, label: String, nChanges: int}]
		 */
		lastChangesCounters: {
			default: []
		},

		/**
		 * Timestamp of service creation.
		 */
		serviceCreationTime: {
			default: -1
		},

		/**
		 * Date range of currently stored history data (allChanges).
		 */
		currDataRange: { //TODO populate and use in change_range effect
			default: {
				start: -1,
				end: -1
			}
		},

		/**
		 * Stores buffer of changes that were received from the timeline API.
		 * Flat array.
		 */
		allChanges: {
			default: []
		},

		/**
		 * Stores sub-range of change for the displayRange.
		 * Flat array.
		 */
		displayChanges: {
			default: []
		},

		/**
		 * Stores buffer of severities ran that were received from the timeline API.
		 * Flat array.
		 */
		severityRanges: {
			default: []
		},

		timelineCandidates: {
			default: {
				displayRange: null
			}
		},

		/**
		 * container for the data that defines state of the timeline.
		 * All sub-properties should be updated together to ensure valid state of the timeline component.
		 */
		timelineData: {
			selectable: true,
			default: getEmptyTimelineData()
		},
	},
	actionHandlers: {
		[effectInitHistoryMode.ACTION_TYPES.MAP_INIT_HISTORY]: createActionConfig(
			effectInitHistoryMode.effectHandlers.effectInitHistoryMode
		),

		[effectReceiveHistoryData.ACTION_TYPES.MAP_HISTORY_DATA_REQUEST]: createActionConfig(
			effectReceiveHistoryData.effectHandlers.getHistoryData
		),

		[effectReceiveHistoryData.ACTION_TYPES.MAP_HISTORY_DATA_REQUEST_SUCCEEDED]: createActionConfig(
			effectReceiveHistoryData.effectHandlers.historyDataResponse
		),

		[effectReceiveHistoryData.ACTION_TYPES.MAP_HISTORY_DATA_SET]: createActionConfig(
			effectResolveHistoryState.effectHandlers.resolveHistoryState
		),

		[ACTION_TYPES_CHANGE_CARD_SELECTED.TOPO_CHANGE_CARD_SELECTED]: 
			createActionConfig(effectHighlightTimelineEvent),

		[effectOnTimelineRangeUpdated.ACTION_TYPES.TIMELINE_RANGE_UPDATED]: createActionConfig(
			effectOnTimelineRangeUpdated.effectHandlers.onTimelineRangeUpdated
		),
		[effectOnTimelineEventSelection.ACTION_TYPES.TIMELINE_DATA_SELECTED_UPDATED]: createActionConfig(
			effectOnTimelineEventSelection.effectHandlers.onTimelineEventSelection
		)
	}
};
