

const ACTION_TYPES = {
	'NOW_TOGGLE#CHECKED_SET': 'NOW_TOGGLE#CHECKED_SET'
};

const toggleCheckedEffect = coeffects => {
	const {action, state} = coeffects;
	const {model} = state.properties.mapSettings;
	const {value, itemId} = action.payload;
	const toggleModel = model[itemId];
	
	//invoking original angular function to switch the toggle.
	if (toggleModel && toggleModel.enabled !== value)
		toggleModel.toggle();
};

const effectHandlers = {
	toggleCheckedEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};